export default ([{
    path: '/itReport',
    meta: {
        title: 'IT',
        type: "safem"
    },
    component: resolve => require(['@/views/itReport'], resolve),
    children: [
        {
            path: '/',
            redirect: 'problemsStatistical'
        },
        {
            path: 'templatePage',
            meta: {
                title: ''
            },
            component: resolve => require(['@/components/templatePage'], resolve)
        },
        {
            path: 'problemsStatistical',
            meta: {
                title: '',
                type: "safem"
            },
            component: resolve => require([`@/views/itReport/problemsStatistical`], resolve)
        },
    ]
}])