<template>
  <div class="orgMemberSelect">
    <el-row class="h100">
      <el-col :span="8" class="h100">
        <selectTree
          :treeData="treeData"
          :defaultProps="defaultProps"
          :nodeKey="nodeKey"
          ref="tree2"
          @handleClickTree="backtreeData"
          @clearTableDate="clearTableDate"
        ></selectTree>
      </el-col>
      <el-col :span="16" class="h100">
        <memberList
          :selectConfig="selectConfig"
          ref="memerList"
          :isSeleteName="isSeleteName"
          @searchUserName="searchUserName"
          :chosedData="chosedData"
        ></memberList>
      </el-col>
    </el-row>
  </div>
</template>
<script>
import memberList from "./memberList";
import selectTree from "./treeLoad";
import {
  queryOrgTree, //
  queryDepartment, //
  // queryOrgTreeAuth,
  queryUserList, //
  queryRealNameUser //
} from "@/api/common";
// import {
//   getEmOrg,
//   getEmOrgUser
// } from "@/api/emergencyManagement/emergencyPlan";
// import { getPdOrg, getPdOrgUser } from "@/api/safeOrg/leadGroup";
export default {
  components: { memberList, selectTree },
  props: ["seleteSetting", "otherInfo"],
  data() {
    return {
      currentCode: "",
      selectConfig: {
        radio: false,
        column: [
          {
            label: "",
            prop: "orgNodeName",
            width: 60
          },
        ],
        data: [],
        // 
        queryObj: {
          method: () => {
            this.getTreeUser();
          },
          queryData: {
            orgNodeCode: "",
            page: 1,
            pageCount: 15
          }
        },
        tableDataObj: {
          tableArr: [],
          total: null
        },
        paginationIsshow: false,
        // 
        paginationObj: {
          pageSizes: [15, 30, 50, 100]
        }
      },
      chosedData: [],
      treeData: [],
      defaultProps: {
        label: "label",
        children: "children"
      },
      currentOrg: null,
      nodeKey: ""
    };
  },
  computed: {
    isSeleteName() {
      return ["orgmember", "realNameMember"].includes(
        this.selectConfig.seleteType
      );
    }
  },
  created() {
    this.init();
  },
  methods: {
    init() {
      let { seleteType, chosedData, props } = this.seleteSetting;
      this.selectConfig = Object.assign(
        {},
        this.selectConfig,
        this.seleteSetting
      );
      this.defaultProps = Object.assign({}, this.defaultProps, props);
      this.chosedData = chosedData || [];
      //
      if (seleteType == "org") {
        //keys
        this.$set(this, "nodeKey", "organizationCode");
        // this.getTreeInfo();
      }
      //
      else if (seleteType == "orgmember") {
        //keys
        this.$set(this, "nodeKey", "organizationCode");
        // this.getTreeInfo();
      }
    },
    clearTableDate() {
      this.$refs.memerList.setTableData([]);
    },
    //
    backtreeData(data, type) {
      let { seleteType } = this.seleteSetting;
      this.currentOrg = data;
      let oldData = {
        id: data.id,
        name: data.label
      };
      let newData = [];
      const This = this;
      if (seleteType == "org" || seleteType == "orgAuth") {
        // let newData = this.$utils.convert_tree_data([data]);
        if (type === "search") {
          let tableData = [];
          tableData.push(data);
          this.$refs.memerList.setTableData(tableData);
        } else {
          queryOrgTree(data.id).then(res => {
            newData = res.data;
            newData.map(i => ((i.name = i.name), (i.id = i.code)));
            newData.unshift(oldData);
            this.$refs.memerList.setTableData(newData);
          });
        }
      }
      //
      else if (seleteType == "orgmember") {
        this.getTreeUser(data.id);
      }
      //
    },
    getSeleteData() {
      let data = this.$refs.memerList.getSeleteData();
      return [...data];
    },
    //
    getEmOrgInfo() {
      getEmOrg(this.otherInfo.id).then(res => {
        let newData = this.$utils.getTrees(
          res.data,
          null,
          "id",
          "parentId",
          "orgName"
        );
        this.$refs.tree2.setTreeData(newData);
      });
    },
    //
    getPdOrgInfo() {
      getPdOrg().then(res => {
        let data = res.data.map(item => {
          item.organizationName = item.orgName; //editForm
          return item;
        });
        let newData = this.$utils.getTrees(
          data,
          null,
          "id",
          "parentId",
          "orgName"
        );
        this.$refs.tree2.setTreeData(newData);
      });
    },

    //
    getTreeInfo() {
      //  queryOrgTree(id).then(res =>
      //   {
      //     console.log(res.data);
      //     return res.data;
      //   });
      // console.log(this.otherInfo);
      // if (this.otherInfo) {
      //   queryOrgTree(this.otherInfo.parentCode).then(res =>
      //   {
      //     console.log(res);
      //     let data = res.data;
      //     this.$refs.tree2.setTreeData(data);
      //   });
      // } else {
      //   queryOrgTree("yhszy").then(res => {
      //     // console.log(res);
      //     let data = res.data;
      //     // this.$refs.tree2.setTreeData(data);
      //   });
      // }
    },
    //()
    getAuthTreeInfo() {
      queryOrgTreeAuth().then(res => {
        let data = res.data;
        this.$refs.tree2.setTreeData(data);
      });
    },

    //
    getTreeUser(data) {
      queryUserList(data).then(res => {
        let data = res.data;
        data.map(i => ((i.name = i.name), (i.id = i.code)));
        this.$refs.memerList.setTableData(data);
      });
    },
    //
    getRealNameUser(data) {
      //
      queryRealNameUser(data).then(res => {
        let data = res.data;
        data.map(
          i => (
            (i.userName = i.staffName),
            (i.deptCode = i.orgId),
            (i.deptName = i.orgItem)
          )
        );
        this.$refs.memerList.setTableData(data);
      });
    },
    //
    searchUserName(userName) {
      if (userName) {
        let form = {
          name: userName,
          orgNodeCode: null
        };
        if (this.currentOrg) {
          form.orgNodeCode = this.currentOrg.id;
        }
        //orgNodeCodecode
        // let { seleteType } = this.seleteSetting;
        queryRealNameUser(form).then(res => {
          let data = res.data || [];
          data.map(i => ((i.name = i.name), (i.id = i.code)));
          this.$refs.memerList.setTableData(data);
        });
      }

      // if (seleteType == "orgmember") {
      //   if (userName) {
      //     this.getTreeUser({ userName: userName });
      //   } else if (this.currentOrg) {
      //     this.getTreeUser({ orgCode: this.currentOrg.organizationCode });
      //   }
      // } else if (seleteType == "realNameMember") {
      //   if (userName) {
      //     this.getRealNameUser({ staffName: userName });
      //   } else if (this.currentOrg) {
      //     this.getRealNameUser({ orgId: this.currentOrg.organizationCode });
      //   }
      // }
    },
    //
    searchEmUser(id) {
      getEmOrgUser(id).then(res => {
        let data = res.data || [];
        data.map(i => (i.id = i.userId));
        this.$refs.memerList.setTableData(data);
      });
    },
    //
    searchPdUser(id) {
      getPdOrgUser({ conOrgId: id }).then(res => {
        let data = res.data || [];
        data = data.map(item => {
          delete item.id;
          item.id = item.userId;
          return item;
        });
        this.$refs.memerList.setTableData(data);
      });
    }
  }
};
</script>
<style scoped>
.orgMemberSelect {
  height: 360px;
  border: 1px solid #ebebeb;
  margin-bottom: 10px;
}
</style>