<template>
  <div class="editTable">
    <div class="edit-table-toolbar">
      <div class="edit-table-toolbar__title">{{ title }}</div>
      <div class="edit-btn" v-if="!hideBtn">
        <slot name="btnsBefore"></slot>
        <template v-for="(i, index) in btnConfig">
          <el-button
            class="btn-text-blue"
            size="mini"
            :key="index"
            v-if="showBtn(i.name)"
            :icon="i.icon"
            @click="i.methons"
            >{{ i.text }}</el-button
          >
        </template>
        <slot name="btnsAfter"></slot>
      </div>
    </div>
    <div class="edit-table-content">
      <el-form
        ref="formTable"
        :model="tableConfig"
        :rules="rules"
        label-width="0px"
      >
        <el-table
          :data="tableConfig.data"
          border
          :stripe="stripe"
          ref="editTable"
          :header-cell-style="headerStyle"
          :height="tableConfig.height"
          :max-height="tableConfig.maxHeight"
        >
          <el-table-column
            v-if="tableConfig.select && !hideBtn"
            type="selection"
            fixed="left"
            width="40"
          ></el-table-column>
          <el-table-column
            type="index"
            width="55"
            label=""
          ></el-table-column>
          <template v-for="(item, index) in tableConfig.column">
            <el-table-column
              v-if="!item.isHide"
              :label="item.label"
              :width="item.width"
              :min-width="item.minWidth"
              :prop="item.prop"
              :key="index"
            >
              <template slot-scope="scope">
                <el-form-item
                  :prop="'data.' + scope.$index + '.' + scope.column.property"
                  :rules="rules[item.prop]"
                >
                  <!-- <el-form-item :prop="'data.'+ scope.$index + '.'+item.prop"> -->
                  <template v-if="scope.row.editable">
                    <el-select
                      v-model="scope.row[scope.column.property]"
                      v-if="item.type === 'select'"
                      :multiple="item.multiple"
                      :disabled="item.disabled"
                      @change="
                        val =>
                          changeSelete(
                            val,
                            item.text,
                            scope.$index,
                            item.seleteOption
                          )
                      "
                      size="mini"
                      class="w100"
                    >
                      <template v-for="item_s in item.seleteOption">
                        <el-option
                          v-if="
                            typeof scope.row[scope.column.property] == 'string'
                          "
                          :key="item_s.value"
                          :label="item_s.label"
                          :disabled="
                            item.hasOwnProperty('disabledOp') &&
                              item.disabledOp(item_s.value, tableConfig.data)
                          "
                          :value="'' + item_s.value"
                        ></el-option>
                        <el-option
                          v-else-if="
                            typeof scope.row[scope.column.property] == 'number'
                          "
                          :key="item_s.value"
                          :label="item_s.label"
                          :disabled="
                            item.hasOwnProperty('disabledOp') &&
                              item.disabledOp(item_s.value, tableConfig.data)
                          "
                          :value="
                            ('' + item_s.value).trim() == ''
                              ? ''
                              : parseInt(item_s.value)
                          "
                        ></el-option>
                        <el-option
                          v-else
                          :key="item_s.value"
                          :label="item_s.label"
                          :disabled="
                            item.hasOwnProperty('disabledOp') &&
                              item.disabledOp(item_s.value, tableConfig.data)
                          "
                          :value="item_s.value"
                        ></el-option>
                      </template>
                    </el-select>
                    <el-date-picker
                      v-else-if="
                        [
                          'date',
                          'week',
                          'month',
                          'week',
                          'year',
                          'datetime'
                        ].includes(item.type)
                      "
                      v-model="scope.row[scope.column.property]"
                      :title="scope.row[scope.column.property]"
                      class="w100"
                      :type="item.type"
                      :disabled="item.disabled"
                      :format="dateFrm(item.type)"
                      :value-format="dateFrm(item.type)"
                      size="mini"
                      placeholder=""
                    ></el-date-picker>
                    <el-input
                      v-else-if="item.type === 'dept'"
                      :key="scope.$index"
                      size="mini"
                      type="textarea"
                      rows="2"
                      @click.native="
                        getOrgInfo(
                          scope.$index,
                          scope.row[scope.column.property],
                          item
                        )
                      "
                      v-model="scope.row[scope.column.property]"
                      :title="scope.row[scope.column.property]"
                      readonly
                      class="w100"
                    ></el-input>
                    <el-input
                      v-else-if="item.type === 'number'"
                      :key="scope.$index"
                      size="mini"
                      :readonly="item.readonly"
                      :disabled="item.disabled"
                      :type="item.type || ''"
                      @change="item.method ? item.method(scope.row) : ''"
                      v-model="scope.row[scope.column.property]"
                      :title="scope.row[scope.column.property]"
                      @wheel.native.prevent="stopScrollFun($event, item.type)"
                      class="w100"
                    ></el-input>
                    <el-input
                      v-else
                      :key="scope.$index"
                      size="mini"
                      :readonly="item.readonly"
                      :disabled="item.disabled"
                      :type="item.type || ''"
                      :rows="item.rows || 1"
                      @change="item.method ? item.method(scope.row) : ''"
                      v-model="scope.row[scope.column.property]"
                      :title="scope.row[scope.column.property]"
                      class="w100"
                    ></el-input>
                  </template>
                  <template v-else>
                    <el-button
                      type="text"
                      size="small"
                      v-if="item.scopeType && item.scopeType == 'textButton'"
                      @click="
                        item.buttonMethod
                          ? item.buttonMethod(scope.$index, scope.row)
                          : null
                      "
                      >{{ scope.row[scope.column.property] }}</el-button
                    >

                    <template v-else>
                      <el-tooltip
                        effect="dark"
                        v-if="item.showTooltip"
                        placement="top"
                        :content="
                          item.formatter
                            ? item.formatter(
                                scope.row[scope.column.property],
                                scope.row
                              )
                            : item.text
                            ? formatterNull(scope.row[item.text])
                            : formatterNull(scope.row[scope.column.property])
                        "
                      >
                        <span
                          class="table-cloumn__span--tooltip w100"
                          style="display:inline-block"
                          >{{
                            item.formatter
                              ? item.formatter(
                                  scope.row[scope.column.property],
                                  scope.row
                                )
                              : item.text
                              ? formatterNull(scope.row[item.text])
                              : formatterNull(scope.row[scope.column.property])
                          }}</span
                        >
                      </el-tooltip>
                      <span v-else>{{
                        item.formatter
                          ? item.formatter(
                              scope.row[scope.column.property],
                              scope.row
                            )
                          : item.text
                          ? formatterNull(scope.row[item.text])
                          : formatterNull(scope.row[scope.column.property])
                      }}</span>
                    </template>
                  </template>
                </el-form-item>
              </template>
            </el-table-column>
          </template>
        </el-table>
      </el-form>
      <dialogPublic
        :title="''"
        :show.sync="show"
        @doSubmit="addSeleteMember"
        @doClose="() => (show = false)"
      >
        <orgMemberSelect
          ref="orgMemberSelect"
          v-if="show"
          :otherInfo="formData"
          :seleteSetting="orgMemberSelect"
        ></orgMemberSelect>
      </dialogPublic>
    </div>
  </div>
</template>
<script>
export default {
  // props: ["tableSetting", "formData", "hideBtn","stripe"],
  props: {
    tableSetting: {
      type: Object,
      default: () => {
        return {};
      }
    },
    formData: {
      type: Object,
      default: () => {
        return {};
      }
    },
    hideBtn: {
      type: Boolean,
      default: false
    },
    stripe: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      title: "",
      tableConfig: {
        select: true,
        data: [],
        column: []
      },
      btnConfig: [
        {
          name: "add",
          text: "",
          icon: "el-icon-plus",
          methons: () => {
            this.add();
          }
        },
        {
          name: "edit",
          text: "",
          icon: "el-icon-edit",
          methons: () => {
            this.edit();
          }
        },
        {
          name: "delete",
          text: "",
          icon: "el-icon-delete",
          methons: () => {
            this.delete();
          }
        },
        {
          name: "save",
          text: "",
          icon: "el-icon-check",
          methons: () => {
            this.save();
          }
        }
      ],
      btnArr: [],
      //
      orgMemberSelect: {
        seleteType: "orgmember",
        props: {
          label: "organizationName",
          children: "children"
        },
        chosedData: []
      },
      show: false,
      deptItem: {},
      rules: {}
    };
  },
  computed: {
    rowProp() {
      let row = new Object();
      this.tableConfig.column.map(i => {
        row[i.prop] = "";
      });
      return row;
    }
  },

  watch: {
    "tableSetting.maxHeight"() {
      this.tableConfig.maxHeight = this.tableSetting.maxHeight;
    },
    "tableSetting.title"() {
      this.title = this.tableSetting.title;
    },
    "tableConfig.data": {
      handler(val) {
        this.$emit("changeTableData", val);
      },
      deep: true
    }
  },
  created() {
    this.init();
  },
  methods: {
    init() {
      let {
        btnArr,
        column,
        title,
        data,
        select,
        maxHeight,
        height
      } = this.tableSetting;
      this.btnArr = btnArr || [];
      this.title = title || "";
      this.tableConfig.column = column || [];
      this.tableConfig.data = data || [];
      this.tableConfig.select = select;
      this.tableConfig.maxHeight = maxHeight;
      this.tableConfig.height = height;
      this.tableConfig.data.map((i, index) => {
        i.rowIndex = index;
      });

      let rules = {};
      this.tableConfig.column.map(i => {
        if (i.required) {
          rules[i.prop] = [];
          let message = "";
          if (i.type === "text" || i.type === "textarea") {
            message = ``;
          } else {
            message = ``;
          }
          rules[i.prop].push({
            required: true,
            message: message + i.label,
            trigger: i.trigger || "blur"
          });
          if (i.rules) {
            rules[i.prop].push(i.rules);
          }
        }
      });
      this.rules = Object.assign({}, rules);
    },
    headerStyle() {
      return {
        borderRight: "none"
      };
    },
    showBtn(btn) {
      return this.btnArr.indexOf(btn) != -1;
    },
    //
    dateFrm(type) {
      if (type === "date") {
        return "yyyy-MM-dd";
      } else if (type == "datetime") {
        return "yyyy-MM-dd HH:mm:ss";
      } else {
        return "";
      }
    },
    //
    add() {
      let len = this.tableConfig.data.length;
      let rowIndex = 0;
      if (len) {
        rowIndex = this.tableConfig.data[len - 1].rowIndex + 1;
      }
      let nRow = Object.assign(
        {},
        { rowIndex: rowIndex, editable: true },
        this.rowProp
      );

      if (this.tableSetting.addMethons) {
        this.$emit(
          "handleClickAdd",
          nRow,
          JSON.parse(JSON.stringify(this.tableConfig.data))
        );
      } else {
        //
        this.tableConfig.data.push(nRow);
      }
    },
    //
    edit() {
      let selectRow = this.$refs.editTable.selection;
      let seleteInput = this.tableConfig.column.filter(
        i => i.type == "select" && i.multiple
      );
      seleteInput = seleteInput.map(i => i.prop);

      selectRow.map(i => {
        this.tableConfig.data.map((j, index) => {
          if (i.rowIndex == j.rowIndex) {
            j.editable = true;
            seleteInput.map(k => {
              if (!Array.isArray(j[k])) {
                j[k] = j[k] ? j[k].split(",") : [];
              }
            });
            this.$set(this.tableConfig.data, index, j);
          }
        });
      });
    },
    //
    async delete() {
      try {
        let selectRow = this.$refs.editTable.selection;
        if (this.tableSetting.hasOwnProperty("deleteMethods")) {
          let res = await this.tableSetting.deleteMethods(selectRow);
        }
        this.tableConfig.data = [...this.tableConfig.data].filter(x =>
          [...selectRow].every(y => y.rowIndex !== x.rowIndex)
        );
      } catch (error) {
        console.log(error);
      }
    },
    //
    save() {
      this.$refs.formTable.validate(valid => {
        if (valid) {
          let { data } = this.tableConfig;
          data = JSON.parse(JSON.stringify(data));
          data.forEach(i => {
            delete i.rowIndex;
            delete i.editable;
          });
          this.$emit("save", data);
        } else {
          this.$message.error("");
        }
      });
    },
    //
    getFormData() {
      let { data } = this.tableConfig;
      // data.forEach(i => {
      //   delete i.rowIndex;
      //   delete i.editable;
      // });
      return data;
    },
    getTableData(needValid = true) {
      return new Promise((resolve, reject) => {
        let { data } = this.tableConfig;
        data = JSON.parse(JSON.stringify(data));
        data.forEach(i => {
          delete i.rowIndex;
          delete i.editable;
        });
        if (!needValid) {
          resolve(data);
          return;
        }
        this.$refs.formTable.validate(valid => {
          if (valid) {
            resolve(data);
          } else {
            reject("");
          }
        });
      });
    },
    //
    setTableData(data) {
      data = JSON.parse(JSON.stringify(data));
      data.map((i, index) => (i.rowIndex = index));
      this.$set(this.tableConfig, "data", [...data]);
    },
    //data
    pushData(data) {
      let arr = [...data];
      arr.map(i => {
        this.tableConfig.data.push(i);
      });
    },
    //
    changeSelete(val, text, index, option) {
      if (text) {
        let type = typeof val;
        let valText = "";
        let arr = [];
        if (type == "string") {
          arr = option.filter(i => i.value == val);
        } else {
          arr = [...option].filter(x => [...val].some(y => x.value == y));
        }
        if (arr) {
          arr = arr.map(i => {
            return i.label;
          });
          valText = arr.join();
        }
        this.tableConfig.data[index][text] = valText;
      }
    },
    //
    getOrgInfo(index, value, item) {
      this.deptItem = Object.assign({}, { _index: index }, item);

      let arr = value ? value.split(",") : [];
      let ids = this.tableConfig.data[index][item.orgIdsStr] || "";
      let idsArr = ids ? ids.split(",") : [];
      let chosedData = [];

      this.orgMemberSelect = {
        seleteType: item.seleteType,
        props: item.treeProps,
        // chosedData: chosedData,
        column: [
          {
            label: "",
            prop: "orgName"
          }
        ]
      };

      arr.map((i, index) => {
        let obj = {};
        obj.id = idsArr[index];
        obj[this.orgMemberSelect.column[0].prop] = arr[index];
        chosedData.push(obj);
      });

      this.orgMemberSelect.chosedData = chosedData;

      this.show = true;
    },
    //
    addSeleteMember() {
      let data = this.$refs.orgMemberSelect.getSeleteData();
      let names = "";
      let ids = "";
      if (data) {
        names = data
          .map(i => {
            return i[this.orgMemberSelect.column[0].prop];
          })
          .join();
        ids = data
          .map(i => {
            return i.id;
          })
          .join();
      }
      let row = this.tableConfig.data[this.deptItem._index];
      row[this.deptItem.orgIdsStr] = ids;
      row[this.deptItem.prop] = names;
      this.$set(this.tableConfig.data, this.deptItem._index, row);
      this.show = false;
    },
    /**- */
    formatterNull(text) {
      if (text === null || text === undefined || text === "") {
        return "-";
      } else {
        return text;
      }
    },
    /** */
    setDefalutData(form) {
      this.tableConfig.data.map((i, index) => {
        let obj = Object.assign({}, i, form);
        this.$set(this.tableConfig.data, index, obj);
      });
    },
    /** */
    stopScrollFun(evt, type) {
      if (type !== "number") {
        return true;
      }
      evt = evt || window.event;
      if (evt.preventDefault) {
        // Firefox
        evt.preventDefault();
        evt.stopPropagation();
      } else {
        // IE
        evt.cancelBubble = true;
        evt.returnValue = false;
      }
      return false;
    }
  }
};
</script>
<style lang="scss" >
.editTable {
  border: 1px solid #e1e1e1;
  margin-bottom: 10px;
  input {
    text-align: center;
  }
  .el-form-item__content {
    font-size: 16px;
    line-height: 20px;
    white-space: pre-wrap;
  }
  .el-form-item {
    margin-bottom: 0px;
  }
  .edit-table-toolbar {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 10px 0;
  }
  .edit-table-toolbar__title {
    font-weight: bold;
    margin-left: 10px;
  }
  .table-cloumn__span--tooltip {
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
  }
  .el-table td,.el-table th {
    text-align: center;
  }
}
</style>