<template>
  <div class="pdf__viewer h100 w100">
    <div class="pdf__viewer--btn">
      <el-tag size="mini"> {{pageTotalNum}} </el-tag>
      <el-tag size="mini" type="danger" v-show="pageTotalNum > 100">{{maxNum}}</el-tag>
      <el-button icon="el-icon-zoom-out" type="text" v-show="scale > 100" @click="scaleX"></el-button>
      <el-button icon="el-icon-refresh" type="text" @click="refresh"></el-button>
      <el-button icon="el-icon-zoom-in" type="text" @click="scaleD"></el-button>
    </div>
    <div class="h100 w100">
      <template v-if="pageTotalNum">
        <pdf
          ref="myPdfComponent"
          class="pdf__content"
          v-for="num in maxNum"
          @error="pdfError($event)"
          :key="num"
          :page="num"
          :src="pdfUrl"
        />
      </template>
    </div>
  </div>
</template>

<script>
import pdf from "vue-pdf";
export default {
  props: ["src"],
  components: { pdf },
  data() {
    return {
      pdfUrl: "",
      scale: 100, //
      pageNum: 1,
      pageTotalNum: 0
    };
  },
  created() {
    this.init();
  },
  computed: {
    maxNum() {
      let num = this.pageTotalNum > 100 ? 100 : this.pageTotalNum;
      return num;
    }
  },
  mounted() {},
  methods: {
    init() {
      this.pdfUrl = pdf.createLoadingTask(this.src);
      this.loadPdfHandler();
    },
    //
    loadPdfHandler() {
      this.pdfUrl.promise.then(pdf => {
        this.pageTotalNum = pdf.numPages;
      });
    },
    //
    scaleD() {
      this.scale += 20;
      this.$refs.myPdfComponent.map(i => {
        i.$el.style.width = parseInt(this.scale) + "%";
      });
      //   this.$refs.myPdfComponent.$el.style.width = parseInt(this.scale) + "%";
    },
    pdfError(e) {
      console.log(e);
    },

    //
    scaleX() {
      if (this.scale == 100) {
        return;
      }
      this.scale += -20;
      this.$refs.myPdfComponent.map(i => {
        i.$el.style.width = parseInt(this.scale) + "%";
      });
      //   this.$refs.myPdfComponent.$el.style.width = parseInt(this.scale) + "%";
    },
    //
    refresh() {
      this.scale = 100;

      this.$refs.myPdfComponent.map(i => {
        i.$el.style.width = parseInt(this.scale) + "%";
      });
      //   this.$refs.myPdfComponent.$el.style.width = parseInt(this.scale) + "%";
    }
  }
};
</script>

<style>
.pdf__viewer {
  /* position: relative; */
}
.pdf__viewer--btn {
  display: block;
  position: absolute;
  z-index: 2;
  right: 20px;
  top: 60px;
}
.pdf__viewer--btn .el-button {
  font-size: 20px;
}
.pdf__content {
  border-bottom: 1px solid #000;
}
</style>