<template>
  <div class="org-member-tree h100">
    <div class="filter__box">
      <el-input
        placeholder=""
        size="mini"
        class="treeInput"
        v-model="filterText"
        clearable
      ></el-input>
    </div>
    <div class="tree__box" v-if="isThree">
      <el-tree
        class="filter-tree"
        highlight-current
        :default-expand-all="defaultExpand"
        :node-key="nodeKey"
        :data="data"
        :props="props"
        lazy
        :load="loadNode"
        @node-click="handleNodeClick"
        ref="tree"
      ></el-tree>
    </div>
    <div class="department_box" v-else-if="!isThree">
      <!-- <baseTable 
        :tableData="tableSetting" 
        :hideBtn="hideBtn" 
        @row-click="chooseDepartment"
        ref="baseTable">
      </baseTable> -->
      <el-table
        :data="tableConfig.data"
        highlight-current-row
        ref="gridData"
        @row-click="selectOne"
        height="calc(100% - 41px)"
      >
        <el-table-column
          v-if="tableConfig.radio"
          fixed="left"
          label=""
          width="35"
        >
          <template slot-scope="scope">
            <el-radio
              v-model="radioCheck"
              :label="scope.row.id"
              @change="i => radioSelect(scope.row)"
            >
              <i></i>
            </el-radio>
          </template>
        </el-table-column>
        <el-table-column
          v-else
          type="selection"
          fixed="left"
          width="40"
        ></el-table-column>
        <template v-for="(item, index) in tableConfig.column">
          <el-table-column
            :label="item.label"
            :min-width="item.width"
            :prop="item.prop"
            :key="index"
          ></el-table-column>
        </template>
      </el-table>
    </div>
  </div>
</template>

<script>
import {
  queryOrgTree, //
  queryDepartment //
} from "@/api/common";
import baseTable from "@/components/table/baseTable";
export default {
  props: {
    treeData: {
      type: Array,
      default: () => {
        return [];
      }
    },
    clickExpand: {
      type: Boolean,
      default: false
    },
    defaultExpand: {
      type: Boolean,
      default: false
    },
    defaultExpandKeys: {
      type: Array,
      default: () => {
        return [];
      }
    },
    nodeKey: {
      type: String,
      default: ""
    }
  },
  components: { baseTable },
  data() {
    return {
      filterText: "",
      props: {
        label: "label",
        children: [],
        isLeaf: "isLeaf"
      },
      tableConfig: {
        radio: true,
        data: [],
        column: [
          {
            label: "",
            prop: "name",
            width: 60
          }
        ]
      },
      radioCheck: null,
      hideBtn: false,
      data: [],
      departmentData: [],
      isThree: true,
      resolve: null
    };
  },
  watch: {
    //
    filterText(val) {
      if (val === "") {//
        this.isThree=true,
        this.requestTree(this.resolve);
        //
         this.$emit("clearTableDate");
      } else {
        this.getDepartment(val);
      }
    }
  },

  created() {
    this.init();
  },
  methods: {
    init() {
      // this.data = JSON.parse(JSON.stringify(this.treeData));
      // this.props = Object.assign({}, this.defaultProps);
    },
    //
    getDepartment(data) {
      this.isThree = false;
      let form = {
        name: data
      };
      queryDepartment(form)
        .then(res => {
          let data = res.data.list || [];
          data.map(i => ((i.id = i.code)));
          // this.departmentData = data;
          this.radioCheck = data[0].id;//
          this.tableConfig.data = data;
          this.$emit("handleClickTree", data[0], "search");
          // this.$refs.tree2.setTreeData(data);
        })
        .catch(res => {
          console.log(res);
        });
    },
    chooseDepartment() {},
    //
    radioSelect(row) {
      let arr = [];
      arr.push(Object.assign({}, row));
      // this.hadChoseData = [...arr];
    },
    //
    selectOne(row, column) {
      this.radioCheck = row.id;
      this.$emit("handleClickTree", row, "search");
      // if (this.tableConfig.radio) {
      //   this.radioCheck = row.id;
      //   this.radioSelect(row);
      // } else {
      //   this.$refs.gridData.toggleRowSelection(row);
      //   this.select([], row);
      // }
    },

    //
    handleNodeClick(node, resolve) {
      this.$emit("handleClickTree", node);
    },
    //
    refreshLoad() {
      this.loadNode.forEach(item => {
        this.loadChildren(item.node, {}, item.resolve);
      });
    },
    //
    loadNode(node, resolve) {
      if (node.level === 0) {
        //
        this.resolve = resolve;
        this.requestTree(resolve);
      } else {
        //
        this.getIndex(node.data.id, resolve);
        // this.$emit("handleClickTree", node.data);
      }
    },
    //
    requestTree(resolve) {
      queryOrgTree("yhszy").then(res => {
        let data = res.data;
        //
        this.setTreeData(data, resolve);
      });
    },
    //
    getIndex(id, resolve) {
      queryOrgTree(id).then(res => {
        let data = res.data;
        //
        this.setTreeData(data, resolve);
      });
    },
    //
    getClickchild(id, resolve) {
      queryOrgTree(id).then(res => {
        let data = res.data;
        this.setTreeData(data, resolve);
        //
      });
    },
    //
    setTreeData(data, resolve) {
      let treeData = [];
      data.map(i => {
        let obj = {
          id: i.code,
          label: i.name,
          isLeaf: i.isCatalog ? false : true
        };
        treeData.push(obj);
      });
      resolve(treeData);
      // this.$set(this, "data", treeData);
    }
  }
};
</script>

<style>
.org-member-tree
  .el-tree--highlight-current
  .el-tree-node.is-current
  > .el-tree-node__content {
  background-color: #bedaf9 !important;
}
</style>

<style lang="scss" scoped>
.org-member-tree {
  .treeInput {
    width: 80%;
  }
  .filter__box {
    padding: 4px;
  }
  .tree__box {
    height: calc(100% - 36px);
    overflow: auto;
  }
}
.department_box {
  width: 100%;
  height: 100%;
  padding: 10px;
  font-size: 16px;
}
</style>