<template>
  <el-dialog
    :title="dialogTitle"
    :visible.sync="visible"
    :append-to-body="appendTobody"
    :close-on-click-modal="false"
    :fullscreen="fullscreen"
    :width="width"
    class="dialogPublic"
    @close="doClose"
  >
    <slot></slot>
    <div slot="footer" class="dialog-footer" ref="addReceptionForm" v-if="!noFootBtn">
      <slot name="footer__btn"></slot>
      <el-button
        type="primary"
        class="dialog-footer__btn"
        size="mini"
        @click="doSubmit"
      >{{btnText[0]}}</el-button>
      <el-button size="mini" class="dialog-footer__btn" @click="doClose">{{btnText[1]}}</el-button>
    </div>
  </el-dialog>
</template>

<script>
export default {
  props: {
    title: {
      type: String,
      default: ""
    },
    show: {
      type: Boolean,
      default: false
    },
    appendTobody: {
      type: Boolean,
      default: false
    },
    noFootBtn: {
      type: Boolean,
      default: false
    },
    fullscreen: {
      type: Boolean,
      default: false
    },
    btnText: {
      type: Array,
      default: () => {
        return ["", ""];
      }
    },
    width: {
      type: String,
      default: ""
    },
    height: {
      type: String,
      default: ""
    }
  },
  name: "dialogPublic",
  data() {
    return {
      dialogTitle: "",
      visible: false
    };
  },
  watch: {
    visible(val) {
      if (!val) {
        this.$emit("update:show", false);
      } else {
        this.resizeHeight();
      }
    },
    show: {
      immediate: true,
      handler(show) {
        this.init();
      }
    }
  },
  created() {
    this.init();
  },
  mounted() {
    // this.init();
    // this.resizeHeight();
  },
  methods: {
    init() {
      (this.dialogTitle = this.title), (this.visible = this.show);
    },
    doSubmit() {
      this.$emit("doSubmit", true);
    },
    doClose() {
      this.$emit("doClose", true);
    },
    //
    resizeHeight() {
      if (!this.fullscreen) {
        this.$nextTick(() => {
          let bH = document.body.clientHeight;
          let bW = document.body.clientWidth;
          let dialog = document.getElementsByClassName("el-dialog");
          let diglogH = dialog[0].clientHeight;
          let diglogW = dialog[0].clientWidth;

          let dialogBody = document.getElementsByClassName("el-dialog__body");
          let diglogBodyH = dialog[0].clientHeight;
          let diglogBodyW = dialog[0].clientWidth;

          // diglogH = diglogH > bH * 0.8 ? bH * 0.8 : diglogH || 500;
          if (this.height) {
            dialogBody[0]["style"].height = this.height;
            return;
          }

          if (diglogH > bH * 0.8) {
            let h = bH * 0.8;
            dialog[0]["style"].height = h + "px";
          }

          if (diglogBodyH < bH * 0.45) {
            let h = bH * 0.45;
            dialogBody[0]["style"].height = h + "px";
          }

          if (bW < 1366) {
            dialog[0]["style"].width = bW * 0.8 + "px";
          }
        });
      }
    }
  }
};
</script>

<style lang="scss">
.dialogPublic {
  .dialog-footer__btn {
    letter-spacing: 2px;
  }
  .el-dialog__header{
    background-color: $systemColor;
    
  }
  .el-dialog__title{
    color: #ffffff;
  }
  .el-dialog__headerbtn .el-dialog__close{
    color: #f0f0f0;
  }
}
</style>