<template>
  <div class="asset-selete">
    <dialogPublic
      title=""
      :show.sync="showDialong"
      @doSubmit="doSave"
      @doClose="doClose"
      fullscreen
      appendTobody
    >
      <seleteTable
        :tableSetting="tableSetting"
        :hadData="hadSeleteData"
        ref="seleteTable"
      ></seleteTable>
    </dialogPublic>
  </div>
</template>

<script>
import seleteTable from "@/components/table/seleteTable";
import { querySupplierList } from "@/api/common";
export default {
  props: {
    hadSeleteData: {
      type: Array,
      default: new Array()
    },
    showSelete: {
      type: Boolean,
      default: true
    },
  },
  watch: {
  },
  components: { seleteTable },
  data() {
    return {
      tableSetting: {
        maxHeight: "600px",
        column: [
          {
            label: "",
            prop: "comNumber"
          },
          {
            label: "",
            prop: "comFullName"
          },
          {
            label: "",
            prop: "comAddress"
          },
          {
            label: "",
            prop: "comTel"
          },

          {
            label: "",
            prop: "comPerson"
          },
          {
            label: "",
            prop: "personMobile"
          },
          {
            label: "",
            prop: "remark"
          }
        ],
        data: []
      },
      showDialong: true,
    };
  },
  created() {
    this.getDevelopers();
  },
  methods: {
    async getDevelopers() {
      let res = await querySupplierList();
      let data = res.data.list;
      // console.log(res);
      this.$refs.seleteTable.setTableData(data || []);
    },
    doClose() {
      this.$emit("update:showSelete", false);
    },
    doSave() {
      let list = this.$refs.seleteTable.getSeleteData();
      if (list.length == 1) {
        this.$emit("changeSelete", list);
        this.doClose();
      } else {
        this.$message.warning("");
      }
    },
    
  }
};
</script>

