<template>
  <div class="editTree">
    <div class="edit-tree-toolbar">
      <div>{{title}}</div>
      <div class="edit-btn" v-if="!hideBtn">
        <template v-for="(i,index) in btnConfig">
          <el-button
            class="btn-text-blue"
            size="mini"
            :key="index"
            v-if="showBtn(i.name)"
            :icon="i.icon"
            @click="i.methons"
          >{{i.text}}</el-button>
        </template>
      </div>
    </div>
    <div class="edit-tree-content">
      <el-tree
        class="filter-tree"
        :data="treeConfig.data"
        node-key="id"
        :props="defaultProps"
        :expand-on-click-node="clickExpand"
        default-expand-all
        highlight-current
        @node-click="treeNodeClick"
        ref="tree"
      >
        <span class="custom-tree-node" slot-scope="{ node,data }">
          <span>{{ node.label }}</span>
          <span v-if="currentNode.id===data.id&&!hideBtn&&data.id!==-1">
            <el-button
              type="text"
              @click.native="()=>TNodeAdd(node,data)"
              class="btnIcon"
              icon="el-icon-plus"
            ></el-button>
            <el-button
              type="text"
              @click.native="()=>TNodeEdit(node,data)"
              class="btnIcon"
              icon="el-icon-edit-outline"
            ></el-button>
            <el-button
              type="text"
              @click.native="()=>TNodeDel(node,data)"
              class="btnIcon"
              icon="el-icon-delete"
            ></el-button>
          </span>
        </span>
      </el-tree>
    </div>
    <dialogPublic
      :title="title"
      :show.sync="show"
      v-if="show"
      @doSubmit="doSubmit"
      @doClose="doClose"
    >
      <editForm :formConfig="formConfig" ref="editForm" :formData="editFormData"></editForm>
    </dialogPublic>
  </div>
</template>
<script>
export default {
  props: ["treeSetting", "haveRemark", "hideBtn"],

  data() {
    const data = [
      {
        id: 1,
        label: " 1",
        children: [
          {
            id: 4,
            label: " 1-1",
            children: [
              {
                id: 9,
                label: " 1-1-1"
              },
              {
                id: 10,
                label: " 1-1-2"
              }
            ]
          }
        ]
      },
      {
        id: 2,
        label: " 2",
        children: [
          {
            id: 5,
            label: " 2-1"
          },
          {
            id: 6,
            label: " 2-2"
          }
        ]
      },
      {
        id: 3,
        label: " 3",
        children: [
          {
            id: 7,
            label: " 3-1"
          },
          {
            id: 8,
            label: " 3-2"
          }
        ]
      }
    ];
    return {
      title: "",
      treeConfig: {
        select: true,
        data: JSON.parse(JSON.stringify(data))
      },
      btnConfig: [
        {
          name: "add",
          text: "",
          icon: "el-icon-plus",
          methons: () => {
            this.add();
          }
        },
        {
          name: "edit",
          text: "",
          icon: "el-icon-edit",
          methons: () => {
            this.edit();
          }
        },
        {
          name: "delete",
          text: "",
          icon: "el-icon-delete",
          methons: () => {
            this.delete();
          }
        }
      ],
      btnArr: [],
      defaultProps: {
        children: "children",
        label: "orgName"
      },
      currentNode: {}, //
      show: false,
      formConfig: {
        itemArr: [
          {
            label: "",
            prop: "parentName",
            type: "text",
            disabled: true,
            span: 24
          },
          {
            label: "",
            prop: "orgName",
            type: "text",
            span: 24,
            required: true
          },

          {
            label: "",
            prop: "orgOrder",
            type: "number",
            span: 24,
            required: true
          }
        ]
      },
      editFormData: {},
      remarkItem: {
        label: "",
        prop: "remark",
        type: "text",
        span: 24
      }
    };
  },
  created() {
    this.init();
  },
  watch: {
    treeSetting: {
      handler(val) {
        this.$set(this.treeConfig, "data", val.data || []);
      },
      deep: true
    }
  },
  methods: {
    init() {
      let { btnArr, title, data, defaultProps, clickExpand } = this.treeSetting;
      this.btnArr = btnArr || [];
      this.clickExpand = clickExpand || false; //
      this.defaultProps = Object.assign({}, this.defaultProps, defaultProps);
      this.title = title || "";
      this.$set(this.treeConfig, "data", data || []);
      if (this.haveRemark) {
        this.formConfig.itemArr.push(this.remarkItem);
      }
    },
    //
    showBtn(btn) {
      return this.btnArr.indexOf(btn) != -1;
    },
    //
    add() {
      this.show = true;
      // let { id, orgName, parentId, parentName } = this.currentNode;
      this.editFormData = {
        orgName: "",
        parentId: "",
        parentName: ""
      };
    },
    //
    edit(node) {},
    //
    delete() {},
    //
    getFormData() {
      let { data } = this.treeConfig;
      data.map(i => delete i.rowIndex);
      return data;
    },
    //
    treeNodeClick(node) {
      this.currentNode = JSON.parse(JSON.stringify(node));
      this.$emit("handleClickTree", node);
    },
    //
    doSubmit() {
      this.$refs.editForm.getFormData().then(res => {
        this.$emit("save", res);
      });
    },
    //
    doClose() {
      this.show = false;
    },
    //
    TNodeAdd(node, data) {
      this.show = true;
      let { id, orgName, parentId, parentName } = data;
      this.editFormData = {
        orgName: "",
        parentId: id,
        parentName: orgName
      };
    },
    //
    TNodeEdit(node, data) {
      this.show = true;
      this.editFormData = Object.assign({}, data);
    },
    //
    TNodeDel(node, data) {
      this.$confirm("" + data.orgName + ", ?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning"
      })
        .then(() => {
          this.$emit("delete", data.id);
        })
        .catch(e => {});
    },
    setNoNode() {
      this.$refs.tree.setCurrentKey(null);
      this.currentNode = {};
    },
    getCurrentKey() {
      return this.$refs.tree.getCurrentKey();
    },
    //
    setTreeData(data) {
      this.$set(this.treeConfig, "data", data);
      setTimeout(() => {
        let node = {};
        if (data.length) {
          let dataArr = this.$utils.convert_tree_data(data);
          let obj = dataArr.find(i => i.id == this.currentNode.id);
          node = obj ? this.currentNode : data[0];
        }
        this.$refs.tree.setCurrentKey(node.id);
        this.treeNodeClick(node);
      }, 0);
    },
    //
    choseDialog() {
      this.show = false;
    }
  }
};
</script>
<style>
.editTree
  .el-tree--highlight-current
  .el-tree-node.is-current
  > .el-tree-node__content {
  background-color: #bedaf9 !important;
}
</style>
<style lang="scss" scoped>
.editTree {
  border: 1px solid #EBEEF5;
  min-height: 300px;
  max-height: 600px;
  overflow-y: auto;
  overflow-x: auto;
  margin-bottom: 10px;
}

.edit-tree-toolbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px;
}
.custom-tree-node {
  flex: 1;
  display: flex;
  align-items: center;
  justify-content: space-between;
  font-size: 14px;
  padding-right: 8px;
}
</style>