<template>
  <div class="attachForm">
    <el-upload
      ref="upload"
      action="#"
      :on-change="changeFile"
      :file-list="newUploadObj.files"
      name="fileList"
      :class="{attachFormUpload:newUploadObj.files.length,hideClass:disabled}"
      :auto-upload="false"
      :accept="acceptType"
      :limit="limit"
      :multiple="multiple"
    >
      <el-button slot="trigger" size="mini" type="primary" v-if="!disabled"></el-button>
      <div class="attach-form_tip" v-if="title&&!disabled">Tip: {{title}}</div>
      <div slot="file" slot-scope="{file}">
        <p>
          {{file.name}}
          <span class="upload-btn-in">
            <el-button
              type="text"
              size="mini"
              icon="el-icon-zoom-in"
              v-show="showPreBtn(file)"
              @click="preFile(file)"
              title=""
            ></el-button>
            <el-button
              type="text"
              size="mini"
              icon="el-icon-download"
              v-if="file.id"
              @click="downloadFile(file)"
              title=""
            ></el-button>
            <el-button
              type="text"
              size="mini"
              icon="el-icon-delete"
              @click="removeFile(file)"
              title=""
              v-if="!disabled"
            ></el-button>
          </span>
        </p>
      </div>
    </el-upload>
    <el-image-viewer v-if="showViewer" :on-close="closeViewer" :url-list="[preurl]" />
    <dialogPublic
      :title="''"
      :show.sync="showPdfViewer"
      appendTobody
      fullscreen
      noFootBtn
      @doClose="doClose"
    >
      <template v-if="showPdfViewer">
        <pdf-viewer ref="myPdfComponent" v-if="showPdf" :src="preurl" />
        <div class="h100 w100" v-else v-html="htmlText"></div>
      </template>
    </dialogPublic>
  </div>
</template>
<script>
// 
import ElImageViewer from "element-ui/packages/image/src/image-viewer";
import pdfViewer from "@/components/pdfViewer/index";
import { downloadFile } from "@/api/common";
export default {
  props: {
    filesList: {
      type: Array,
      default: () => {
        return [];
      }
    },
    disabled: {
      type: Boolean,
      default: false
    },
    accept: {
      type: Array,
      default: () => {
        return [];
      }
    },
    limit: {
      type: Number,
      default: null
    },
    multiple: {
      type: Boolean,
      default: false
    },
    title: {
      type: String,
      default: ""
    },
    rules: null
  },
  components: { ElImageViewer, pdfViewer },
  data() {
    return {
      newUploadObj: {
        deleteId: [],
        files: []
      },
      showViewer: false, // 
      showPdfViewer: false, //pdf
      showPdf: false,
      preurl: "", //URL
      htmlText: ""
    };
  },
  created() {
    this.init();
  },
  computed: {
    acceptType() {
      return this.accept.join();
    }
  },
  watch: {
    "newUploadObj.files": {
      handler(val) {
        let { deleteId, files } = this.newUploadObj;
        let filesObj = { deleteId, files };
        this.$emit("changeFilesField", filesObj);
      },
      deep: true
    },
    filesList: {
      handler(val) {
        if (val.length && val[0] != "ok") {
          this.init();
        }
      },
      deep: true
    }
  },
  methods: {
    init() {
      this.filesList.map(i => {
        // let obj = Object.assign({}, i);
        this.newUploadObj.files.push(i);
      });
    },
    removeFile(file) {
      let arr = this.newUploadObj.files.filter(i => {
        return i.uid != file.uid;
      });
      this.newUploadObj.files = arr || [];
      if (file.id) {
        this.newUploadObj.deleteId.push(`${file.id}`);
      }
    },
    changeFile(file, fileList) {
      if (this.beforeUpload(file.raw, fileList)) {
        this.newUploadObj.files.push(file.raw);
      } else {
        fileList.map((i, index) => {
          if (i.uid == file.uid) {
            this.$refs.upload.uploadFiles.splice(index, 1);
          }
        });
      }
    },
    // 
    closeViewer() {
      this.showViewer = false;
    },
    //
    doClose() {
      this.showPdfViewer = false;
      this.showPdf = false;
    },
    //
    async preFile(row) {
      /**PDF */
      // let typeArr = ["pdf"];
      // if (typeArr.includes(row.extension)) {
      //   window.open(row.attachmentUrl);
      //   return true;
      // }
      // if (row.pdfUrl) {
      //   window.open(row.pdfUrl);
      // } else {
      //   this.preurl = row.attachmentUrl;
      //   this.showViewer = true;
      // }
      let { attachmentName: fileName, attachmentUrl, pdfUrl } = row;

      let path = pdfUrl || attachmentUrl;
      downloadFile({ fileName, path }).then(res => {
        const blob = new Blob([res]);
        const url = window.URL.createObjectURL(blob);
        this.preurl = url;

        let pathStep = path.lastIndexOf("."); //
        let pathLen = path.length; //
        let type = path.substring(pathStep + 1, pathLen);
        let typeArr = ["jpg", "jpeg", "gif", "bmp", "png"];

        if (typeArr.includes(type)) {
          this.showViewer = true;
        } else if (["htm", "html"].includes(type)) {
          this.htmlText = "";
          var reader = new FileReader();
          reader.readAsText(blob);
          reader.onload = () => {
            this.htmlText = reader.result; //
          };
          this.showPdfViewer = true;
        } else {
          this.showPdf = true;
          this.showPdfViewer = true;
        }
      });
    },
    //
    doDownload(res, fileName) {
      const blob = new Blob([res]);
      const url = window.URL.createObjectURL(blob);
      const elink = document.createElement("a");
      elink.download = fileName;
      elink.style.display = "none";
      elink.href = url;
      document.body.appendChild(elink);
      elink.click();
      URL.revokeObjectURL(elink.href); // URL 
      document.body.removeChild(elink);
    },
    // 
    downloadFile(row) {
      let path = row.attachmentUrl;
      let fileName = row.attachmentName + "." + row.extension;
      // downloadFile({ fileName, path }).then(res => {
      //   this.doDownload(res, fileName);
      // });
      this.$utils.downloadFile(path, fileName);
    },
    //
    showPreBtn(file) {
      let typeArr = ["jpg", "jpeg", "gif", "bmp", "png", "pdf"];
      if (file.id) {
        if (file.pdfUrl) {
          return true;
        } else {
          return typeArr.includes(file.extension);
        }
      } else {
        return false;
      }
    },
    //
    beforeUpload(file, fileList) {
      
      let typeArr = file.name.split(".");
      let type = typeArr[typeArr.length - 1];
      let isUpload = true;

      if (this.acceptType) {
        if (this.acceptType.indexOf(type) == -1) {
          this.$message.error("");
          isUpload = false;
        }
      }
      if (this.rules) {
        let rulesResult = this.rules(file, fileList);
        if (!rulesResult.success) {
          isUpload = false;
          this.$message.error(rulesResult.msg || "");
        }
      }
      return isUpload;
    },
    //
    returnFilsObj() {
      let { deleteId, files } = this.newUploadObj;
      let filesObj = { deleteId, files };
      return filesObj;
    }
  }
};
</script>
<style lang="scss">
.upload-btn-in {
  position: absolute;
  right: 5px;
}
.attachFormUpload .el-upload-list {
  border: 1px solid #ebebeb;
  padding: 0 10px 10px 10px;
}

.attach-form_tip {
  display: inline-block;
  font-size: 12px;
  color: #8a8a8a;
  line-height: 18px;
  margin-left: 10px;
}
.hideClass .el-upload {
  display: none;
}
</style>