<template>
  <div class="editTable">
    <div class="edit-table-toolbar">
      <div>{{ title }}</div>
      <div class="edit-btn" v-if="!hideBtn">
        <template v-for="(i, index) in btnConfig">
          <el-button
            class="btn-text-blue"
            size="mini"
            :key="index"
            v-if="showBtn(i.name)"
            :icon="i.icon"
            @click="i.methons"
            >{{ i.text }}</el-button
          >
        </template>
      </div>
    </div>
    <div class="edit-table-content">
      <el-table
        :data="tableData"
        border
        stripe
        ref="editTable"
        :header-cell-style="headerStyle"
        :max-height="tableConfig.maxHeight || 400"
      >
        <el-table-column
          v-if="tableConfig.select"
          type="selection"
          fixed="left"
          width="40"
        ></el-table-column>
        <el-table-column type="index" width="55" label=""></el-table-column>
        <template v-for="(item, index) in tableConfig.column">
          <el-table-column
            :label="item.label"
            :width="item.width"
            :prop="item.prop"
            :key="index"
          >
            <template slot-scope="scope">
              {{
                item.formatter
                  ? item.formatter(scope.row)
                  : scope.row[scope.column.property]
              }}
            </template>
          </el-table-column>
        </template>
        <el-table-column label="" fixed="right" align="center">
          <template slot-scope="scope">
            <el-button
              @click="downItem(scope.row)"
              type="text"
              v-if="scope.row.id"
              size="small"
              title=""
              icon="el-icon-download"
            ></el-button>
            <el-button
              @click="deleteItem(scope.row)"
              v-if="scope.row.id"
              type="text"
              size="small"
              title=""
              icon="el-icon-delete"
            ></el-button>
            <el-button
              @click="preItem(scope.row)"
              v-show="showPreBtn(scope.row)"
              type="text"
              size="small"
              title=""
              icon="el-icon-view"
            ></el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <dialogPublic
      :title="''"
      :show.sync="show"
      @doSubmit="addToTable"
      class="editTable--dialog"
      appendTobody
      @doClose="() => (show = false)"
    >
      <el-upload
        class="upload-demo"
        action
        :auto-upload="false"
        drag
        multiple
        :file-list="fileList"
        :on-remove="fileChange"
        :on-change="fileChange"
      >
        <i class="el-icon-upload"></i>
        <div class="el-upload__text">
          
          <em></em>
        </div>
        <!-- <div class="el-upload__tip" slot="tip">jpg/png500kb</div> -->
      </el-upload>
    </dialogPublic>
    <el-image-viewer
      v-if="showViewer"
      :on-close="closeViewer"
      :url-list="[preurl]"
    />
    <dialogPublic
      :title="''"
      :show.sync="showPdfViewer"
      appendTobody
      fullscreen
      noFootBtn
      @doClose="doClose"
    >
      <template v-if="showPdfViewer">
        <pdf-viewer ref="myPdfComponent" v-if="showPdf" :src="preurl" />
        <div class="h100 w100" v-else v-html="htmlText"></div>
      </template>
    </dialogPublic>
  </div>
</template>
<script>
// 
import ElImageViewer from "element-ui/packages/image/src/image-viewer";
import pdfViewer from "@/components/pdfViewer/index";
import {
  uploadAttach, //
  downloadFile, //
  deleteFiles, //
  getAttachList //
} from "@/api/common";
export default {
  props: {
    attachSetting: {
      type: Object,
      default: () => {
        return { sourceId: "" };
      }
    },
    hideBtn: {
      type: Boolean,
      default: false
    }
  },
  components: { ElImageViewer, pdfViewer },
  data() {
    return {
      title: "",
      tableConfig: {
        select: true,
        data: [],
        column: [
          {
            label: "",
            prop: "annexName"
            // width: 150,
          },
          {
            label: "",
            prop: "suffix",
            width: 100
          },
          {
            label: "",
            prop: "state",
            width: 100,
            formatter: row => {
              return row.id ? "" : "";
            }
          },
          {
            label: "",
            prop: "createName"
            // width: 150,
          },
          {
            label: "",
            prop: "createTime"
            // width: 150,
          }
        ]
      },
      btnConfig: [
        {
          name: "add",
          text: "",
          icon: "el-icon-plus",
          methons: () => {
            this.add();
          }
        },
        // {
        //   name: "download",
        //   text: "",
        //   icon: "el-icon-download",
        //   methons: () => {
        //     this.download();
        //   }
        // },
        // {
        //   name: "delete",
        //   text: "",
        //   icon: "el-icon-delete",
        //   methons: () => {
        //     this.delete();
        //   }
        // },
        {
          name: "save",
          text: "",
          icon: "el-icon-check",
          methons: () => {
            this.save();
          }
        }
      ],
      tableData: [],
      currentId: "",
      newFileArr: [],
      btnArr: [],
      fileList: [],
      deleteIdArr: [], //ID
      show: false,
      showViewer: false, // 
      showPdfViewer: false, //pdf
      showPdf: false,
      preurl: "", //URL
      htmlText: ""
    };
  },
  watch: {
    //
    "attachSetting.sourceId"(val) {
      if (val) {
        this.getFileList();
      }
    },
  },
  created() {
    this.init();
  },
  methods: {
    init() {
      let { btnArr, title, data, select, sourceId } = this.attachSetting;
      this.btnArr = btnArr || [];
      this.title = title || "";
      this.tableConfig.data = data || [];
      this.tableConfig.select = select;
      this.currentId = sourceId;
      this.tableConfig.data.map((i, index) => {
        i.rowIndex = index;
      });
      if (sourceId) {
        this.getFileList();
      }
    },
    headerStyle() {
      return {
        borderRight: "none"
      };
    },
    showBtn(btn) {
      return this.btnArr.indexOf(btn) != -1;
    },
    //
    getFileList() {
      let sourceId = this.attachSetting.sourceId;
      getAttachList(sourceId).then(res => {
        let data = res.data || [];
        this.tableData = data;
      });
    },
    add() {
      if (this.attachSetting.sourceId) {
        this.show = true;
        this.fileList = [];
      } else {
        this.$message.warning("");
      }
    },
    //
    doClose() {
      this.showPdfViewer = false;
      this.showPdf = false;
    },
    //
    fileChange(file, fileList) {
      //
      this.fileList = fileList;
    },
    //
    showPreBtn(row) {
      let typeArr = ["jpg", "jpeg", "gif", "bmp", "png", "pdf"];
      if (row.id) {
        if (row.pdfUrl) {
          return true;
        } else {
          return typeArr.includes(row.extension);
        }
      } else {
        return false;
      }
    },
    //
    async preItem(row) {
      /**PDF */
      // let typeArr = ["pdf"];
      // if (typeArr.includes(row.extension)) {
      //   window.open(row.attachmentUrl);
      //   return true;
      // }
      // if (row.pdfUrl) {
      //   window.open(row.pdfUrl);
      // } else {
      //   this.preurl = row.attachmentUrl;
      //   this.showViewer = true;
      // }
      let { attachmentName: fileName, attachmentUrl, pdfUrl } = row;

      let path = pdfUrl || attachmentUrl;
      downloadFile({ fileName, path }).then(res => {
        const blob = new Blob([res]);
        const url = window.URL.createObjectURL(blob);
        this.preurl = url;

        let pathStep = path.lastIndexOf("."); //
        let pathLen = path.length; //
        let type = path.substring(pathStep + 1, pathLen);
        let typeArr = ["jpg", "jpeg", "gif", "bmp", "png"];

        if (typeArr.includes(type)) {
          this.showViewer = true;
        } else if (["htm", "html"].includes(type)) {
          this.htmlText = "";
          var reader = new FileReader();
          reader.readAsText(blob);
          reader.onload = () => {
            this.htmlText = reader.result; //
          };
          this.showPdfViewer = true;
        } else {
          this.showPdf = true;
          this.showPdfViewer = true;
        }
      });
    },
    //
    doDownload(res, fileName) {
      const blob = new Blob([res]);
      const url = window.URL.createObjectURL(blob);
      const elink = document.createElement("a");
      elink.download = fileName;
      elink.style.display = "none";
      elink.href = url;
      document.body.appendChild(elink);
      elink.click();
      URL.revokeObjectURL(elink.href); // URL 
      document.body.removeChild(elink);
    },
    // 
    downItem(row) {
      this.$utils.downloadFile(
        this.attachSetting.billType,
        row.id,
        row.annexName
      );
    },

    // 
    closeViewer() {
      this.showViewer = false;
    },
    //
    addToTable() {
      this.newFileArr = [];
      this.fileList.map(i => {
        //
        let { name, raw } = i;
        let pathStep = name.lastIndexOf("."); //
        let pathLen = name.length; //
        let type = name.substring(pathStep + 1, pathLen); //
        let obj = {
          annexName: name, //
          suffix: type //
        };
        this.tableData.push(obj);
        this.newFileArr.push(raw);
      });
      this.show = false;

      // let len = this.tableConfig.data.length; //
      // let rowIndex = 0;
      // if (len) {
      //   //
      //   rowIndex = this.tableConfig.data[len - 1].rowIndex + 1; //
      // }
      // this.fileList.map(i => {
      //   //
      //   let { name, raw } = i;
      //   let pathStep = name.lastIndexOf("."); //
      //   let pathLen = name.length; //
      //   let type = name.substring(pathStep + 1, pathLen); //
      //   rowIndex++;
      //   let obj = {
      //     name: name, //
      //     extension: type, //
      //     file: raw, //
      //     rowIndex: rowIndex //
      //   };
      //   this.tableConfig.data.push(obj);
      // });
    },
    //
    delete() {
      let selectRow = this.$refs.editTable.selection; //
      this.tableConfig.data = [...this.tableConfig.data].filter(x =>
        [...selectRow].every(y => {
          if (y.rowIndex == x.rowIndex) {
            if (y.id) {
              this.deleteIdArr.push(y.id); //ID
            }
            return false;
          } else {
            return true;
          }
        })
      );
    },
    //
    deleteItem(data) {
      //
      let form = {
        annexName: data.annexName,
        billType: data.billType,
        id: data.id
      };
      this.$confirm(
        "" + data.annexName + ", ?",
        "",
        {
          confirmButtonText: "",
          cancelButtonText: "",
          type: "warning"
        }
      )
        .then(() => {
          deleteFiles(form)
            .then(res => {
              this.getFileList();
              this.$message.warning("");
            })
            .catch(e => {
              this.$message.warning("");
              console.log(e);
            });
        })
        .catch(e => {});
    },
    //
    save() {
      let fileObj = this.newFileArr;
      let formData = new FormData();
      if (this.attachSetting.sourceId && fileObj.length) {
        fileObj.map(i => {
          formData.append("files", i);
        });
        let form = {
          billType: this.attachSetting.billType, //  asset_regist
          sourceId: this.attachSetting.sourceId //id
        };
        uploadAttach(form, formData)
          .then(res => {
            this.getFileList();
            this.$message.warning("");
          })
          .catch(res => {
            this.$message.warning("");
            console.log(res);
          });
      }
    },
    //
    download() {
      let selectRow = this.$refs.editTable.selection;
      selectRow.map(i => {
        i;
        if (i.attachmentUrl) {
          let fileName = i.attachmentName + "." + i.extension;
          this.$utils.downloadFile(i.attachmentUrl, fileName);
        }
      });
    },
    //
    getFormData() {
      let { data } = this.tableConfig;
      let newData = {
        deleteId: this.deleteIdArr.join(),
        data: data
      };
      return newData;
    },
    //
    setTableData(data) {
      this.deleteIdArr = [];
      data = JSON.parse(JSON.stringify(data));
      data.map((i, index) => (i.rowIndex = index));
      this.$set(this.tableConfig, "data", [...data]);
    }
  }
};
</script>
<style scoped>
.edit-table-toolbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px 0;
}
</style>
<style lang="scss">
.editTable--dialog {
  border: 1px solid #e1e1e1;
  margin-bottom: 10px;
  .upload-demo .el-upload {
    width: 100%;
    text-align: center;
    .el-upload-dragger {
      display: inline-block;
    }
  }
}
</style>
