<template>
  <div class="edit-form-box">
    <el-form
      :label-width="editFormConfig.labelWidth"
      :model="editFormData"
      :rules="editFormConfig.rules"
      ref="editFormData"
    >
      <el-row>
        <el-col
          :span="item.span || 8"
          v-for="(item, index) in editFormConfig.itemArr"
          :key="index"
        >
          <el-form-item
            :label="item.label"
            :prop="item.prop"
            :title="item.label"
            :class="{ h36: !['upload', 'textarea'].includes(item.type) }"
            v-if="!item.isHide"
          >
            <slot :name="item.prop" :item="item" :editFormInfo="editFormData">
              <el-input-number
                size="mini"
                v-model="editFormData[item.prop]"
                v-if="item.type === 'el-number'"
                :precision="item.precision"
                :step="item.step"
                :max="item.max"
                :min="item.min"
                :step-strictly="item.stepStrictly"
                :disabled="item.disabled"
                controls-position="right"
                :placeholder="item.placeholder || ''"
                @change="val => inputChange(val, item)"
                class="w100"
              ></el-input-number>
              <el-select
                v-model="editFormData[item.prop]"
                v-else-if="item.type === 'select'"
                :disabled="item.disabled"
                :readonly="item.readonly"
                :filterable="item.filterable"
                :clearable="item.clearable"
                size="mini"
                class="w100"
                :multiple="item.multiple"
                @change="val => selectChange(val, item)"
                :placeholder="item.placeholder || ''"
              >
                <template v-for="item_s in item.seleteOption">
                  <el-option
                    v-if="typeof editFormData[item.prop] == 'string'"
                    :key="item_s.value"
                    :label="item_s.label"
                    :value="'' + item_s.value"
                    :disabled="
                      item.disabledOp
                        ? item.disabledOp(item_s.value, item.seleteOption)
                        : null
                    "
                  ></el-option>
                  <el-option
                    v-else-if="typeof editFormData[item.prop] == 'number'"
                    :key="item_s.value"
                    :label="item_s.label"
                    :value="
                      ('' + item_s.value).trim() == ''
                        ? ''
                        : parseInt(item_s.value)
                    "
                    :disabled="
                      item.disabledOp
                        ? item.disabledOp(item_s.value, item.seleteOption)
                        : null
                    "
                  ></el-option>
                  <el-option
                    v-else
                    :key="item_s.value"
                    :label="item_s.label"
                    :value="item_s.value"
                    :disabled="
                      item.disabledOp
                        ? item.disabledOp(item_s.value, item.seleteOption)
                        : null
                    "
                  ></el-option>
                </template>
              </el-select>
              <el-date-picker
                v-else-if="
                  [
                    'date',
                    'week',
                    'month',
                    'year',
                    'datetime',
                    'daterange',
                    'datetimerange'
                  ].includes(item.type)
                "
                v-model="editFormData[item.prop]"
                :title="editFormData[item.prop]"
                class="w100"
                :type="item.type"
                size="mini"
                @change="val => inputChange(val, item)"
                :placeholder="item.placeholder || ''"
                range-separator=""
                start-placeholder=""
                end-placeholder=""
                :disabled="item.disabled"
                :picker-options="item.pickerOptions"
                :readonly="item.readonly"
                :clearable="item.clearable"
                :format="dateFrm(item.type)"
                :value-format="dateFrm(item.type)"
              ></el-date-picker>

              <el-time-picker
                :is-range="item.type === 'timerange'"
                v-else-if="['time', 'timerange'].includes(item.type)"
                v-model="editFormData[item.prop]"
                :title="editFormData[item.prop]"
                size="mini"
                class="w100"
                @change="val => inputChange(val, item)"
                range-separator=""
                start-placeholder=""
                end-placeholder=""
                :placeholder="item.placeholder || ''"
                :picker-options="item.pickerOptions"
                :disabled="item.disabled"
                :readonly="item.readonly"
                :clearable="item.clearable"
              ></el-time-picker>
              <el-time-select
                v-else-if="item.type == 'timeSelect'"
                v-model="editFormData[item.prop]"
                :title="editFormData[item.prop]"
                size="mini"
                class="w100"
                @change="val => inputChange(val, item)"
                :placeholder="item.placeholder || ''"
                :picker-options="item.pickerOptions"
                :disabled="item.disabled"
                :readonly="item.readonly"
                :clearable="item.clearable"
              ></el-time-select>
              <!--  -->
              <el-cascader
                v-model="editFormData[item.prop]"
                v-else-if="
                  [
                    'provinceAndCity',
                    'provinceAndCityPlus',
                    'regionSelect',
                    'regionSelectPlus',
                    'el-cascader'
                  ].includes(item.type)
                "
                :disabled="item.disabled"
                :readonly="item.readonly"
                :separator="item.separator"
                :filterable="item.filterable"
                :clearable="item.clearable"
                :title="editFormData[item.prop]"
                size="mini"
                class="w100"
                :options="cascaderOp[item.type] || item.seleteOption"
                @change="val => selectCascaderChange(val, item)"
                :placeholder="item.placeholder || ''"
              ></el-cascader>

              <template v-else-if="item.type == 'upload'">
                <el-checkbox-group
                  v-model="editFormData[item.prop]"
                ></el-checkbox-group>
                <attach-form
                  :filesList="editFormData[item.prop]"
                  class="w100"
                  :disabled="item.disabled || item.readonly"
                  :accept="item.accept"
                  :limit="item.limit"
                  :title="item.title"
                  :multiple="item.multiple"
                  :rules="item.rules || null"
                  @changeFilesField="
                    filesObj => changeFilesField(filesObj, item)
                  "
                ></attach-form>
              </template>

              <el-input
                v-else-if="
                  [
                    'user',
                    'user-realNameMember',
                    'user-pdMember',
                    'dept',
                    'dept-orgAuth',
                    'dept-pdOrg'
                  ].includes(item.type)
                "
                size="mini"
                @change="val => inputChange(val, item)"
                v-model="editFormData[item.prop]"
                :title="editFormData[item.prop]"
                :placeholder="item.placeholder || ''"
                readonly
                :disabled="item.disabled"
              >
                <el-button
                  slot="append"
                  :icon="
                    ['user', 'user-pdMember'].includes(item.type)
                      ? 'el-icon-user'
                      : 'el-icon-location'
                  "
                  :disabled="item.disabled || item.readonly"
                  @click="goSeleteOrgMember(item, item.type)"
                ></el-button>
              </el-input>
              <el-input
                v-else-if="
                  [
                    'userMultiple',
                    'userMultiple-pdMember',
                    'deptMultiple',
                    'deptMultiple-pdOrg'
                  ].includes(item.type)
                "
                size="mini"
                type="textarea"
                rows="2"
                v-model="editFormData[item.prop]"
                :placeholder="item.placeholder || ''"
                :title="editFormData[item.prop]"
                @click.native="goSeleteOrgMember(item, item.type)"
                @change="val => inputChange(val, item)"
                :disabled="item.disabled"
                readonly
                class="w100"
              ></el-input>

              <el-autocomplete
                v-else-if="item.type == 'autocomplete'"
                v-model="editFormData[item.prop]"
                :fetch-suggestions="querySearchAsync(item)"
                class="w100"
                size="mini"
                :placeholder="item.placeholder || ''"
                :title="editFormData[item.prop]"
                @select="obj => handleSelectAuto(obj, item)"
                @input="val => changeAuto(val, item)"
                :disabled="item.disabled"
                :clearable="item.clearable"
              ></el-autocomplete>
              <el-radio-group
                class="w100"
                size="mini"
                v-model="editFormData[item.prop]"
                v-else-if="item.type == 'radio'"
              >
                <el-radio
                  v-for="(item_s, index) in item.radioOption"
                  :key="index"
                  :label="item_s.value"
                  :disabled="item.disabled"
                  >{{ item_s.label }}</el-radio
                >
              </el-radio-group>
              <el-input
                v-else
                size="mini"
                :type="item.type || ''"
                :rows="item.rows || 1"
                v-model="editFormData[item.prop]"
                :placeholder="item.placeholder || ''"
                :title="editFormData[item.prop]"
                @change="val => inputChange(val, item)"
                :disabled="item.disabled"
                :readonly="item.readonly"
                :clearable="item.clearable"
                class="w100"
              >
                <span slot="suffix" v-if="item.suffixText">{{
                  item.suffixText
                }}</span>
              </el-input>
            </slot>
          </el-form-item>
        </el-col>
      </el-row>
    </el-form>
    <dialogPublic
      :title="''"
      :show.sync="showMember"
      @doSubmit="addSeleteMember"
      @doClose="() => (showMember = false)"
      appendTobody
    >
      <orgMemberSelect
        ref="orgMemberSelect"
        v-if="showMember"
        :seleteSetting="orgMemberSelect"
        :otherInfo="otherInfo"
      ></orgMemberSelect>
    </dialogPublic>
  </div>
</template>

<script>
/** */
import {
  provinceAndCityData,
  regionData,
  provinceAndCityDataPlus,
  regionDataPlus
} from "element-china-area-data";
export default {
  props: ["formData", "formConfig", "otherInfo"],
  data() {
    return {
      editFormData: {},
      editFormConfig: {
        labelWidth: "100px",
        rules: {},
        itemArr: []
      },
      showMember: false,
      currentItem: {},
      orgMemberSelect: {},
      cascaderOp: {
        provinceAndCity: provinceAndCityData,
        regionSelect: regionData,
        provinceAndCityPlus: provinceAndCityDataPlus,
        regionSelectPlus: regionDataPlus
      }
    };
  },
  created() {
    this.init();
  },
  watch: {
    formData(val) {
      this.editFormData = Object.assign({}, val);
    },
  },
  // mounted() {
  //   this.editFormData = Object.assign({}, this.formData);
  // },
  methods: {
    init() {
      this.editFormData = Object.assign({}, this.formData);
      this.editFormConfig = Object.assign(
        {},
        this.editFormConfig,
        this.formConfig
      );
      let rules = {};
      //
      this.formConfig.itemArr.map(i => {
        rules[i.prop] = [];
        if (i.required) {
          rules[i.prop] = [];
          let message = "";
          if (
            i.type === "text" ||
            i.type === "textarea" ||
            i.type === "el-number"
          ) {
            message = ``;
          } else {
            message = ``;
          }
          rules[i.prop].push({
            required: true,
            message: message + i.label,
            trigger: i.trigger || "blur"
          });
        }
        if (i.rules && i.type != "upload") {
          rules[i.prop].push(i.rules);
        }
        if (i.ruleArr && i.type != "upload") {
          rules[i.prop].push(...i.ruleArr);
        }
      });

      this.editFormConfig.rules = Object.assign({}, rules);
    },
    //
    getFormData() {
      return new Promise((resolve, reject) => {
        this.$refs.editFormData.validate(valid => {
          if (valid) {
            let data = Object.assign({}, this.editFormData);
            resolve(data);
          } else {
            reject("");
          }
        });
      });
    },
    //
    dateFrm(type) {
      if (type === "date" || type === "daterange") {
        return "yyyy-MM-dd";
      } else if (type == "datetime" || type === "datetimerange") {
        return "yyyy-MM-dd HH:mm:ss";
      } else if (type == "month") {
        return "yyyy-MM";
      } else if (type == "year") {
        return "yyyy";
      } else {
        return "";
      }
    },
    //
    handleClickSelect() {},
    //
    goSeleteOrgMember(item, type) {
      if (item.disabled) {
        return false;
      }
      this.currentItem = item;
      let value = this.editFormData[item.prop] || ""; //
      let ids = this.editFormData[item.idsStr] || ""; //id

      let arr = value ? value.split(",") : []; //
      let idsArr = ids ? ids.split(",") : []; //id

      let chosedData = []; //
      let typePre = type.split("-")[0]; //type
      let typeBre = type.split("-")[1];
      if (typePre == "dept" || typePre == "deptMultiple") {
        //type
        this.orgMemberSelect = {
          //orgMemberSelect
          seleteType: typeBre ? typeBre : "org",
          radio: typePre == "dept", //
          props: {
            label: "organizationName",
            children: "children"
          },
          column: [
            {
              label: "",
              prop: "name"
            }
          ],
          chosedData: []
        };
        arr.map((i, index) => {
          //
          let obj = {};
          obj.id = idsArr[index];
          obj[this.orgMemberSelect.column[0].prop] = arr[index];
          chosedData.push(obj);
        });
      } else if (typePre == "user" || typePre == "userMultiple") {
        this.orgMemberSelect = {
          seleteType: typeBre ? typeBre : "orgmember",
          radio: typePre == "user", //
          props: {
            label: "organizationName",
            children: "children"
          },
          column: [
            {
              label: "",
              prop: "name",
              width: 60
            }
            // {
            //   label: "",
            //   prop: "phone",
            //   width: 100,
            // },
          ],
          chosedData: []
        };
        //
        let deptCode = item.deptCodeStr
          ? this.editFormData[item.deptCodeStr] || ""
          : ""; //code
        let deptName = item.deptStr
          ? this.editFormData[item.deptStr] || ""
          : ""; //
        let phone = item.phoneStr ? this.editFormData[item.phoneStr] || "" : ""; //
        let deptCodeArr = deptCode ? deptCode.split(",") : []; //
        let deptNameArr = deptName ? deptName.split(",") : [];
        let phoneArr = phone ? phone.split(",") : [];
        arr.map((i, index) => {
          //
          let obj = {};
          obj.id = idsArr[index];

          obj.deptCode = deptCodeArr[index] || "";
          obj.deptName = deptNameArr[index] || "";
          obj.phone = phoneArr[index] || "";

          obj[this.orgMemberSelect.column[0].prop] = arr[index];
          chosedData.push(obj);
        });
      }
      this.orgMemberSelect.chosedData = chosedData;
      this.showMember = true;
    },
    //
    addSeleteMember() {
      let data = this.$refs.orgMemberSelect.getSeleteData();
      let names = "";
      let ids = "";
      if (data) {
        names = data
          .map(i => {
            return i[this.orgMemberSelect.column[0].prop];
          })
          .join();
        ids = data
          .map(i => {
            return i.id;
          })
          .join();
      }
      if (this.currentItem.idsStr) {
        this.$set(this.editFormData, this.currentItem.idsStr, ids);
      }
      this.$set(this.editFormData, this.currentItem.prop, names); //this.editFormData[this.currentItem.prop] = names;
      this.showMember = false;
      if (this.currentItem.methods) {
        this.currentItem.methods(data, this.editFormData);
      }
    },
    //
    setFormItem(prop, data) {
      this.$set(this.editFormData, prop, data);
    },
    //
    getFormItem(prop) {
      return this.editFormData[prop];
    },
    //
    selectChange(val, item) {
      if (item.methods) {
        let ops = item.seleteOption;
        if (item.multiple === true) {
          let arr = ops.filter(i => val.indexOf(i.value) > -1);
          item.methods(arr, this.editFormData);
        } else {
          let obj = ops.find(i => i.value == val);
          item.methods(obj || {}, this.editFormData);
        }
      }
    },
    //
    selectCascaderChange(val, item) {
      if (item.methods) {
        let ops = this.cascaderOp[item.type] || item.seleteOption;
        let allList = this.$utils.convert_tree_data(ops);
        let arr = allList.filter(i => val.includes(i.value));
        item.methods(arr, this.editFormData);
      }
    },
    //
    inputChange(val, item) {
      if (item.methods) {
        item.methods(val, this.editFormData);
      }
    },
    //
    changeFilesField(filesObj, item) {
      this.editFormData[item.fileField] = filesObj;
      let len = filesObj.files.length;
      if (len) {
        this.$set(this.editFormData, item.prop, ["ok"]);
      } else {
        this.$set(this.editFormData, item.prop, []);
      }
    },
    //
    querySearchAsync(item) {
      return (queryString, cb) => {
        var restaurants = item.seleteOption.map(i => {
          return { ...i };
        });
        var results = queryString
          ? restaurants.filter(this.createStateFilter(queryString))
          : restaurants;

        clearTimeout(this.timeout);
        this.timeout = setTimeout(() => {
          cb(results);
        }, 500 * Math.random());
      };
    },
    createStateFilter(queryString) {
      return state => {
        return (
          state.value.toLowerCase().indexOf(queryString.toLowerCase()) !== -1
        );
      };
    },
    //
    handleSelectAuto(obj, item) {
      if (item.selectMethods) {
        item.selectMethods(obj);
      }
    },
    //
    changeAuto(val, item) {
      if (item.changeMethods) {
        item.changeMethods(val);
      }
    }
  }
};
</script>

<style lang="scss">
.edit-form-box {
  .h36 {
    height: 36px;
  }
  .edit-form-box .el-form-item__label {
    width: 125px !important;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
  }
  .el-form-item__content {
    margin-left: 125px !important;
  }
}
</style>