<template>
  <div class="baseTable">
    <el-table
      :data="tableConfig.data"
      border
      stripe
      :header-cell-style="headerStyle"
      :height="tableConfig.autoHeight?null:tableConfig.height||tableConfig.maxHeight||400"
      :cell-style="tableConfig.cellStyle?tableConfig.cellStyle:null"
      ref="tableDataNode"
      @expand-change="expand"
      @row-click="clickChange"
      highlight-current-row
      row-key="id"
    >
      <el-table-column
        v-if="tableConfig.indexShow || false"
        label=""
        type="index"
        width="55"
        fixed
      ></el-table-column>
      <template v-if="tableConfig.type=='expand'">
        <el-table-column type="expand" align="center">
          <template slot-scope="props">
            <div class="expandTable">
              <el-table
                :data="tableConfig.expandData"
                max-height="240"
                border
                stripe
                :header-cell-style="headerStyle"
                v-loading="loading"
                ref="expandTable"
                :cell-style="tableConfig.expandCellStyle?tableConfig.expandCellStyle:null"
              >
                <el-table-column type="index" width="55" label=""></el-table-column>
                <template v-for="(item,index) in tableConfig.expandColumn">
                  <el-table-column
                    :label="item.label"
                    :align="item.textAlign?item.textAlign:null"
                    :width="item.width"
                    :prop="item.prop"
                    v-if="!item.hide"
                    :key="'_'+index"
                  >
                    <template slot-scope="scope">
                      <template v-if="item.type=='button'">
                        <template v-for="(btnItem,btnIndex) in item.buttonArr">
                          <el-button
                            type="text"
                            size="small"
                            :key="btnIndex"
                            v-if="btnItem.isShow(scope.row)"
                            @click="btnItem.methods(scope.row)"
                            :icon="btnItem.icon"
                          >{{btnItem.text}}</el-button>
                        </template>
                      </template>
                      <span
                        :style="item.textStyle(scope.row)"
                        v-else-if="item.textFormatter"
                      >{{item.textFormatter(scope.row)}}</span>
                      <span
                        class="text-pre-wrap"
                        v-else
                      >{{ item.formatter?item.formatter(scope.row):formatterNull(scope.row[scope.column.property])}}</span>
                    </template>
                  </el-table-column>
                </template>
              </el-table>
            </div>
          </template>
        </el-table-column>
      </template>

      <template v-for="(item,index) in tableConfig.column">
        <el-table-column
          :label="item.label"
          :align="item.textAlign?item.textAlign:null"
          :width="item.width"
          :prop="item.prop"
          :key="index"
          v-if="!item.hide"
          :fixed="item.fixed||null"
        >
          <!--  -->
          <template v-if="item.subColumn">
            <template v-for="(subItem,subIndex) in item.subColumn">
              <el-table-column
                :label="subItem.label"
                :align="subItem.textAlign?subItem.textAlign:null"
                :width="subItem.width"
                :prop="subItem.prop"
                :key="subIndex"
                v-if="!subItem.hide"
                :fixed="subItem.fixed||null"
              >
                <template slot-scope="scope">
                  <template v-if="subItem.type=='button'">
                    <template v-for="(btnItem,btnIndex) in subItem.buttonArr">
                      <el-button
                        type="text"
                        size="small"
                        :key="btnIndex"
                        v-if="btnItem.isShow(scope.row)"
                        @click="btnItem.methods(scope.row)"
                        :icon="btnItem.icon"
                      >{{btnItem.text}}</el-button>
                    </template>
                  </template>
                  <template v-else>
                    <span
                      :style="subItem.textStyle(scope.row)"
                      v-if="subItem.textFormatter"
                    >{{subItem.textFormatter(scope.row)}}</span>
                    <el-tooltip
                      effect="dark"
                      v-if="subItem.showTooltip"
                      placement="top"
                      :content="subItem.formatter?subItem.formatter(scope.row):subItem.text?formatterNull(scope.row[subItem.text]):formatterNull(scope.row[scope.column.property])"
                    >
                      <span
                        class="table-cloumn__span--tooltip w100"
                        style="display:inline-block"
                      >{{ subItem.formatter?subItem.formatter(scope.row):subItem.text?formatterNull(scope.row[subItem.text]):formatterNull(scope.row[scope.column.property])}}</span>
                    </el-tooltip>
                    <span
                      v-else
                    >{{ subItem.formatter?subItem.formatter(scope.row):formatterNull(scope.row[scope.column.property])}}</span>
                  </template>
                </template>
              </el-table-column>
            </template>
          </template>
          <template slot-scope="scope">
            <template v-if="item.type=='button'">
              <template v-for="(btnItem,btnIndex) in item.buttonArr">
                <el-button
                  type="text"
                  size="small"
                  :key="btnIndex"
                  v-if="btnItem.isShow(scope.row)"
                  @click="btnItem.methods(scope.row)"
                  :icon="btnItem.icon"
                >{{btnItem.text}}</el-button>
              </template>
            </template>
            <template v-else>
              <span
                :style="item.textStyle(scope.row)"
                v-if="item.textFormatter"
              >{{item.textFormatter(scope.row)}}</span>
              <el-tooltip
                effect="dark"
                v-if="item.showTooltip"
                placement="top"
                :content="item.formatter?item.formatter(scope.row):item.text?formatterNull(scope.row[item.text]):formatterNull(scope.row[scope.column.property])"
              >
                <span
                  class="table-cloumn__span--tooltip w100"
                  style="display:inline-block"
                >{{ item.formatter?item.formatter(scope.row):item.text?formatterNull(scope.row[item.text]):formatterNull(scope.row[scope.column.property])}}</span>
              </el-tooltip>
              <span
                v-else
                class="text-pre-wrap"
              >{{ item.formatter?item.formatter(scope.row):formatterNull(scope.row[scope.column.property])}}</span>
            </template>
          </template>
        </el-table-column>
      </template>
    </el-table>
  </div>
</template>
<script>
export default {
  props: {
    tableData: {
      type: Object,
      default: {},
    },
  },
  data() {
    return {
      tableConfig: {
        column: [],
        expandColumn: [],
        expandData: [],
        data: [],
        noBorderRight: true,
        autoHeight: false,
      },
      loading: false,
      currentrow: {},
    };
  },
  created() {
    this.init();
  },
  computed: {
    headerStyle() {
      if (this.tableConfig.noBorderRight) {
        return {
          borderRight: "none",
        };
      }
      return {};
    },
  },
  methods: {
    init() {
      this.tableConfig = Object.assign({}, this.tableConfig, this.tableData);
    },
    // headerStyle() {
    //   return {
    //     borderRight: "none",
    //   };
    // },
    setTableData(data) {
      this.tableConfig.data = JSON.parse(JSON.stringify(data));
    },
    //data
    pushData(data) {
      let arr = [...data];
      arr.map((i) => {
        this.tableConfig.data.push(i);
      });
    },
    setExpandTableData(data) {
      this.$set(this, "loading", false);

      this.$set(
        this.tableConfig,
        "expandData",
        JSON.parse(JSON.stringify(data))
      );
    },
    clickChange(row) {
      if (this.currentrow) {
        if (this.currentrow.id != row.id) {
          this.$refs["tableDataNode"].toggleRowExpansion(
            this.currentrow,
            false
          );
        } else {
          this.$refs["tableDataNode"].toggleRowExpansion(row, false);
          this.currentrow = null;
          return;
        }
      }
      this.$refs["tableDataNode"].toggleRowExpansion(row, true);
    },
    //
    expand(row, expandrow) {
      if (this.currentrow) {
        if (this.currentrow.id != row.id) {
          this.$refs["tableDataNode"].toggleRowExpansion(
            this.currentrow,
            false
          );
          this.$set(this, "currentrow", row);
        } else {
          this.$set(this, "currentrow", null);
          return;
        }
      }
      this.$set(this, "currentrow", row);
      this.loading = true;
      this.$emit("expandRow", row);
    },
    //table
    getTableData() {
      let arr = JSON.parse(JSON.stringify(this.tableConfig.data));
      return arr;
    },
    /**- */
    formatterNull(text) {
      if (text === null || text === undefined || text === "") {
        return "-";
      } else {
        return text;
      }
    },
  },
};
</script>
<style scoped>
.baseTable {
  height: 100%;
}
.expandTable {
  padding: 10px 40px;
}
.table-cloumn__span--tooltip {
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}
</style>
