export default ([{
    path: '/itAssets',
    meta: {
        title: 'IT',
        type: "safem"
    },
    component: resolve => require(['@/views/itAssets'], resolve),
    children: [
        {
            path: '/',
            redirect: 'assetRegister'
        },
        {
            path: 'templatePage',
            meta: {
                title: ''
            },
            component: resolve => require(['@/components/templatePage'], resolve)
        },
        {
            path: 'assetRegister',
            meta: {
                title: '',
                type: "safem"
            },
            component: resolve => require([`@/views/itAssets/assetRegister`], resolve)
        },
        {
            path: 'assetAllocation',
            meta: {
                title: '',
                type: "safem"
            },
            component: resolve => require([`@/views/itAssets/assetAllocation`], resolve)
        },
        {
            path: 'assetClassification',
            meta: {
                title: '',
                type: "safem"
            },
            component: resolve => require([`@/views/itAssets/assetClassification`], resolve)
        },
        {
            path: 'assetTransfers',
            meta: {
                title: '',
                type: "safem"
            },
            component: resolve => require([`@/views/itAssets/assetTransfers`], resolve)
        },
        {
            path: 'assetRetirement',
            meta: {
                title: '',
                type: "safem"
            },
            component: resolve => require([`@/views/itAssets/assetRetirement`], resolve)
        },
        {
            path: 'assetParameter',
            meta: {
                title: '',
                type: "safem"
            },
            component: resolve => require([`@/views/itAssets/assetParameter`], resolve)
        },
        {
            path: 'myAsset',
            meta: {
                title: '',
                type: "safem"
            },
            component: resolve => require([`@/views/itAssets/myAsset`], resolve)
        },
    ]
}])