<template>
  <div class="asset-selete">
    <dialogPublic
      title=""
      :show.sync="showDialong"
      @doSubmit="doSave"
      @doClose="doClose"
      fullscreen
      appendTobody
    >
      <seleteTable
        :tableSetting="tableSetting"
        :hadData="hadSeleteData"
        ref="seleteTable"
      ></seleteTable>
    </dialogPublic>
  </div>
</template>

<script>
import seleteTable from "@/components/table/seleteTable";
import { getassetsListApi } from "@/api/itAssets/myAsset";
export default {
  props: {
    hadSeleteData: {
      type: Array,
      default: new Array()
    },
    showSelete: {
      type: Boolean,
      default: true
    },
    userID: {
      type: String,
      default: null
    },
  },
  watch: {
    userID(val) {
      if (val) {
        this.getAssets(val);
      }
    }
  },
  components: { seleteTable },
  data() {
    return {
      tableSetting: {
        maxHeight: "600px",
        column: [
          {
            label: "",
            prop: "assetsNumber"
          },
          {
            label: "",
            prop: "assetTypeName"
          },
          {
            label: "",
            prop: "brand"
          },
          {
            label: "",
            prop: "goodsModel"
          },
          {
            label: "",
            prop: "staffunName"
          }
        ],
        data: []
      },
      showDialong: true
    };
  },
  created() {
    if (this.userID) {
      this.getAssets(this.userID);
    }
  },
  methods: {
    async getAssets(userID) {
      let form={
        empId:userID
      };
      // console.log(form);
      let res = await getassetsListApi(form);
      let data = res.data;
      // console.log(res);
      this.$refs.seleteTable.setTableData(data || []);
    },
    doClose() {
      this.$emit("update:showSelete", false);
    },
    doSave() {
      let list = this.$refs.seleteTable.getSeleteData();
      if (list.length == 1) {
        this.$emit("changeSelete", list);
        this.doClose();
      } else {
        this.$message.warning("");
      }
    }
  }
};
</script>

