const getters = {
    //
    systemCode: state => {
        // if (state.app.name) {
        //     return state.app.name
        // }
        return 'AQGL'
    },
    userInfo: state => state.user.userInfo,
    // 
    allAuth: state => state.user.allAuth,
    // 
    isSecondToFirst: state => {
        return false
        // return true
    },
    //
    assetTypeList: state => {
        return state.common.assetTypeList
    },
    //
    spareTypeList: state => {
        return state.common.spareTypeList
    },
    //
    systemType: state => {
        return state.common.systemType
    },
    //
    supplierList: state => {
        return state.common.supplierList
    },
    //  //
    //  appSysList: state => {
    //     return state.common.appSysList
    // },
    // 
    dirtData: (state) => (code, valType = "parameterValue") => {
        let data = []
        if (code) {
            data = state.common.dictData[code] || []
        }
        data.map(i => (i.value = i[valType], i.label = i.parameterName, i.name = i.parameterName))
        return data
    },
    // name
    fineDirtDataName: (state) => (code, value) => {
        let data = code ? state.common.dictData[code] : []
        data = data || []
        let obj = data.find(i => i.parameterValue == value)
        return obj ? obj["parameterName"] : value
    },

    tempIconList: state => {
        return state.common.tempIconList
    },
    areaContrast: state => {
        return state.common.areaContrast
    },

    /** */
    firstNav: state => {
        let firstList = []
        state.user.allAuth && state.user.allAuth.firstNav && (firstList = state.user.allAuth.firstNav)
        return firstList
    },
    /** */
    secondNavObj: state => {
        let secondNav = {}
        state.user.allAuth && state.user.allAuth.secondNav && (secondNav = state.user.allAuth.secondNav)
        return secondNav
    },
    /** */
    currentSecondNav: state => (firstNavCode) => {
        let secondList = state.user.allAuth.secondNav[firstNavCode] || []
        return secondList
    },
    /** */
    currentButtonAuth: state => {
        return state.user.currentButtonAuth
    },
    /** */
    isBtnAuth: state => (btnCode) => {
        let btnArr = state.user.currentButtonAuth
        let obj = btnArr.find(i => i.code == btnCode)
        if (obj) {
            return obj.isJurisdiction == 1
        } else {
            return true
        }
    },
    //
    getFlowStatus: state => status => {
        return state.common.flowStatus[status] || status
    },
    //
    getIsPage: state => isPage => {
        return state.common.isPage[isPage] || isPage
    },
    getModelStatus: state => status => {
        return state.common.modelStatus[status] || status
    }
    
}
export default getters