<template>
  <div class="secondNavIndex h100">
    <div class="nav__left h100" v-show="showNav" v-if="navData.navList.length">
      <secondNavSearch :navData="navData"></secondNavSearch>
    </div>
    <div
      class="main-content h100"
      :class="{navPackUp:!showNav,noSecondNav:!navData.navList.length}"
    >
      <slot></slot>
    </div>
    <div
      class="folding-tree"
      @click="toShowTree"
      :class="{iconPackUp:!showNav}"
      v-if="navData.navList.length"
    >
      <i class="el-icon-arrow-left" v-if="showNav"></i>
      <i class="el-icon-arrow-right" v-else></i>
    </div>
  </div>
</template>

<script>
export default {
  props: ["navData"],
  data() {
    return {
      showNav: true
    };
  },
  methods: {
    toShowTree() {
      this.showNav = !this.showNav;
    }
  }
};
</script>

<style>
.secondNavIndex {
  position: relative;
}
.nav__left {
  float: left;
  width: 200px;
  background-color: #fff;
  overflow: auto;
  position: relative;
}
.main-content {
  margin-left: 220px;
  position: absolute;
  width: calc(100% - 220px);
  background-color: #fff;
  overflow-y: auto;
}
.folding-tree {
  position: absolute;
  line-height: 56px;
  height: 56px;
  width: 12px;
  top: 44%;
  left: 188px;
  border-top-left-radius: 6px;
  border-bottom-left-radius: 6px;
  border-top-right-radius: 0;
  border-bottom-right-radius: 0;
  border: 1px solid #cccccc;
  background-color: #ffffff33;
}
.folding-tree:hover {
  cursor: pointer;
  background-color: #fafafa;
}
.iconPackUp {
  left: -10px;
  border-top-right-radius: 6px;
  border-bottom-right-radius: 6px;
  border-top-left-radius: 0;
  border-bottom-left-radius: 0;
  background-color: #ffffff;
}
.navPackUp {
  margin-left: 0px;
  width: calc(100% - 10px);
}
.noSecondNav {
  margin-left: 0px;
  width: 100%;
}
.el-submenu__title{
      font-size: 16px;
}
