import request from '@/request/api'
/** */
//
export function getFlowList(data) {
    return request({
        url: '/workflow/getWorkflowList.htm',
        method: 'get',
        params: data
    })
}
//
export function getFlowInfo(data) {
    return request({
        url: '/workflow/workflowsubmitpre.htm',
        method: 'get',
        params: data
    })
}
//
export function submitFlowInfo(data) {
    return request({
        url: '/workflow/workflowsubmit.htm',
        method: 'post',
        data: data
    })
}
//
export function allocationEmp(data) {
    return request({
        url: '/workflow/assignPer.htm',
        method: 'post',
        data: data
    })
}