<template>
  <div class="instock-select">
    <dialogPublic
      title=""
      :show.sync="showDialong"
      @doSubmit="doSave"
      @doClose="doClose"
      fullscreen
      appendTobody
    >
      <seleteTable
        :tableSetting="tableSetting"
        :hadData="hadSeleteData"
        ref="seleteTable"
      ></seleteTable>
    </dialogPublic>
  </div>
</template>

<script>
import seleteTable from "@/components/table/seleteTable";
import { queryInstock } from "@/api/common";
export default {
  props: {
    hadSeleteData: {
      type: Array,
      default: new Array(),
    },
    showSelete: {
      type: Boolean,
      default: true,
    },
  },
  watch: {},
  components: { seleteTable },
  data() {
    return {
      tableSetting: {
        maxHeight: "600px",
        column: [
          {
            label: "",
            prop: "orderNo",
          },
          {
            label: "",
            prop: "assetTypeName",
          },
          {
            label: "",
            prop: "goodsModel",
          },
          {
            label: "",
            prop: "qty",
          },

        ],
        data: [],
      },
      showDialong: true,
    };
  },
  created() {
    this.getInstockNo();
  },
  methods: {
    async getInstockNo() {
      let res = await queryInstock();
      let data = res.data;
      // console.log(res);
      this.$refs.seleteTable.setTableData(data || []);
    },
    doClose() {
      this.$emit("update:showSelete", false);
    },
    doSave() {
      let list = this.$refs.seleteTable.getSeleteData();
      if (list.length == 1) {
        this.$emit("changeSelete", list);
        this.doClose();
      } else {
        this.$message.warning("");
      }
    },
  },
};
</script>

