<template>
  <div class="serviceNumberSelect">
    <dialogPublic
      title=""
      :show.sync="showDialong"
      @doSubmit="doSave"
      @doClose="doClose"
      fullscreen
      appendTobody
    >
      <KlTable v-bind="tableConfig" ref="documentTable"></KlTable>
    </dialogPublic>
  </div>
</template>

<script>
import seleteTable from "@/components/table/seleteTable";
import { getServiceOrderNo } from "@/api/common";
export default {
  props: {
    hadSeleteData: {
      type: Array,
      default: new Array()
    },
    showSelete: {
      type: Boolean,
      default: true
    },
    userID: {
      type: String,
      default: null
    }
  },
  watch: {
    // selectionData: {
    //   handler: function(newV,oldV) {
    //     console.log(newV);
    //   },
    //   deep: true
    // }
  },
  components: { seleteTable },
  data() {
    return {
      tableConfig: {
        showFixedHeight: true,
        searchObj: {
          searchMethod: (params, that) => {
            if (this.tableConfig.tableDataObj) {
              this.getServiceList();
            } else {
            }
          },
          searchArr: [
            {
              type: "daterange",
              label: "",
              model: "billDate"
            },
            {
              type: "el-input",
              label: "",
              model: "orderNo",
              placeholder: ""
            },
            {
              type: "el-input",
              label: "",
              model: "staffunName",
              placeholder: ""
            },
            {
              type: "el-input",
              label: "",
              model: "appContent",
              placeholder: ""
            },
            {
              type: "el-input",
              label: "",
              model: "procInstType",
              placeholder: ""
            },
          ]
        },
        // 
        queryObj: {
          method: () => {
            this.getServiceList();
          },
          queryData: {
            page: 1,
            pageSize: 15 //pageCount
          }
        },
        tableDataObj: {
          tableArr: [],
          total: null,
        },
        operateArr: [
          {
            name: "",
            // isHide: !this.$store.getters["isBtnAuth"]("insert"),
            icon: "el-icon-plus",
            method: name => {
              this.toAdd();
            }
          }
          // {
          //   name: "",
          //   icon: "el-icon-download",
          //   // isHide: !this.$store.getters["isBtnAuth"]("export"),
          //   method: name => {
          //     this.toExport();
          //   }
          // }
        ],
        //  table
        tableObj: {
          // 
          selectionShow: true,
          // 
          indexShow: true,
          // table
          tableMethods: {
            selection: val => {
              this.selectionData = val;
            },
            rowMethod: val =>{
              this.selectionData = val;
              // this.doSave()
            }

          },
          // table
          showItems: [
            {
              name: "",
              prop: "orderNo"
            },
            {
              name: "",
              prop: "procInstName",
              id: "procInstType"
            },
            {
              name: "",
              prop: "appContent"
            },
            {
              name: "",
              prop: "staffunName"
            },
            {
              name: "",
              prop: "billDate"
            },
            {
              name: "",
              prop: "stage"
            }
            // {
            //   name: "",
            //   prop: "operate",
            //   width: 150,
            //   scopeType: "button",
            //   fixed: "right",
            //   buttonArr: [
            //     // 
            //     {
            //       name: "",
            //       method: row => {
            //         this.detailsInfo(row);
            //       },
            //       isShow: (row, that) => {
            //         return true;
            //       },
            //       icon: "false"
            //     },
            //   ]
            // }
          ]
        },
        // 
        paginationObj: {
          pageSizes: [15, 30, 50, 100]
        },
        paginationIsshow: true,
        showklTable: false
      },
      selectionData:[],
      showDialong: true
    };
  },
  created() {
    this.getServiceList(this.userID);
  },
  methods: {
    //
    getServiceList() {
      let form = Object.assign({}, this.tableConfig.queryObj.queryData);
      form.pageCount = form.pageSize;
      //
      let { billDate } = form;
      if (billDate) {
        let [beginDate, endDate] = billDate;
        form.beginDate = beginDate;
        form.endDate = endDate;
      }
      this.$delete(form, "billDate");
      this.$delete(form, "pageSize");
      this.$delete(form, "empId");
      getServiceOrderNo(form)
        .then(res => {
          // console.log(res.data);
          this.$set(
            this.tableConfig.tableDataObj,
            "tableArr",
            res.data.list || []
          );
          this.$set(
            this.tableConfig.tableDataObj,
            "total",
            res.data.total || 0
          );
        })
        .catch(res => {
          this.$set(this.tableConfig.tableDataObj, "tableArr", []);
          this.$set(this.tableConfig.tableDataObj, "total", 0);
        });
    },

    doClose() {
      this.$emit("update:showSelete", false);
    },
    doSave() {
      let list = this.selectionData;
      if (list.length == 1) {
        this.$emit("changeSelete", list);
        this.doClose();
      } else {
        this.$message.warning("");
      }
    },

  }
};
</script>

<style lang="scss">
.el-dialog__body {
  height: calc(100% - 110px);
}
.serviceNumberSelect {
}
</style>