export default ([{
    path: '/flowManage',
    meta: {
        title: '',
        type: "safem"
    },
    component: resolve => require(['@/views/flowManage'], resolve),
    children: [
        {
            path: 'templatePage',
            meta: {
                title: ''
            },
            component: resolve => require(['@/components/templatePage'], resolve)
        },
        {
            path: '/',
            redirect: 'pendingApproval'
        },
        {
            path: 'pendingApproval',
            meta: {
                title: '',
                type: "flowManage"
            },
            component: resolve => require([`@/views/flowManage/myApproval/pendingApproval`], resolve)
        },
        {
            path: 'completedApproval',
            meta: {
                title: '',
                type: "flowManage"
            },
            component: resolve => require([`@/views/flowManage/myApproval/completedApproval`], resolve)
        },
        {
            path: 'draftFlow',
            meta: {
                title: '',
                type: "flowManage"
            },
            component: resolve => require([`@/views/flowManage/myFlow/draftFlow`], resolve)
        },
        {
            path: 'examinationFlow',
            meta: {
                title: '',
                type: "flowManage"
            },
            component: resolve => require([`@/views/flowManage/myFlow/examinationFlow`], resolve)
        },
        {
            path: 'endFlow',
            meta: {
                title: '',
                type: "flowManage"
            },
            component: resolve => require([`@/views/flowManage/myFlow/endFlow`], resolve)
        },
    ]
}])