import request from '@/request/api'
import axios from 'axios'
// 
export function getUserInfo() {
    return request({
        url: '/getUserInfo',
        method: 'post'
    })
}
//CODE
export function getDictData(id = "") {
    return request({
        url: '/systemmanger/dictionaryAction/paramListByCodeSysModelBean',
        method: 'post',
        data: { regionCode: id }
    })
}
//
export function getDictDataTree() {
    return request({
        url: '/systemmanger/dictionaryAction/getDictionaryTree.htm',
        method: 'post',
        data: {}
    })
}

// ,
export function queryOrgTree(parentCode = "") {
    return request({
        url: '/systemmanger/deptAction/newDepartmentTree.htm?code=' + parentCode,
        method: 'post'
    })
}

// 
export function queryDepartment(data = {}) {
    return request({
        url: '/systemmanger/deptAction/newDepartmentQuery.htm',
        method: 'post',
        data: data
    })
}

