export function downloadFile(data = {}) {
    return request({
        url: `/AnnexAction/download`,
        method: 'get',
        responseType: 'arraybuffer',
        params: data
    })
}
//
export function deleteFiles(data = {}) {
    return request({
        url: `/AnnexAction/remove`,
        method: 'post',
        data: data
    })
}

