<template>
  <div class="secondNavSearch">
    <!-- <div class="search__box">
      <el-autocomplete
        class="inline-input"
        v-model="searchText"
        id="input-search"
        sizi="mini"
        :fetch-suggestions="querySearch"
        placeholder=""
        suffix-icon="el-icon-search"
        :trigger-on-focus="false"
        @select="handleSelect"
        clearable
      ></el-autocomplete>
    </div> -->
    <div class="second-nav-box">
      <second-Nav :secondNav="navData" v-if="showNav" :uniqueOpened="true"></second-Nav>
    </div>
  </div>
</template>
<script>
export default {
  props: ["navData"],
  data() {
    return { searchText: "", navList: [], showNav: true };
  },
  watch: {
    searchText: function(val) {
      if (val) {
      } else {
      }
      this.$emit("searchData", val);
    },
    navData: {
      handler(val) {
        let isSecondToFirst = this.$store.getters["isSecondToFirst"];
        if (!isSecondToFirst) {
          return true;
        }
        this.showNav = false;
        this.init();
        setTimeout(() => {
          this.showNav = true;
        }, 10);
      },
      deep: true
    }
  },
  mounted() {
    this.init();
  },
  methods: {
    init() {
      let { navList } = this.navData;
      let arr = navList || [];
      let navArr = [];
      const f = function(arr) {
        for (let i = 0; i < arr.length; i++) {
          let item = arr[i];
          let children = item.children;
          if (children && children.length) {
            f(children);
          } else {
            navArr.push({
              value: item.NavName,
              ...item
            });
          }
        }
      };
      f(arr);
      this.navList = [...navArr];
    },
    querySearch(queryString, cb) {
      let restaurants = [...this.navList];
      var results = queryString
        ? restaurants.filter(this.createStateFilter(queryString))
        : restaurants;

      clearTimeout(this.timeout);
      this.timeout = setTimeout(() => {
        cb(results);
      }, 500 * Math.random());
    },
    createStateFilter(queryString) {
      return state => {
        return (
          state.value.toLowerCase().indexOf(queryString.toLowerCase()) !== -1
        );
      };
    },
    handleSelect(item) {
      // let data = item.url;
      // if (/^http/g.test(data)) {
      //   window.open(data, "_self");
      // } else if (/^[a-zA-Z]/g.test(data)) {
      //   this.$router.push({ name: data });
      // } else if (/^\//g.test(data)) {
      //   this.$router.push({ path: data });
      // }
    }
  }
};
</script>
<style>
.secondNavSearch {
  height: 100%;
}
.search__box {
  padding: 10px;
  border-bottom: 1px solid #e8e9ea;
}
.search__box #input-search {
  height: 30px;
  line-height: 30px;
  background-color: #f2f4f5;
  border-radius: 30px;
}
.search__box .inline-input .el-input__icon {
  line-height: 30px;
}
.second-nav-box {
  height: calc(100% - 51px);
}
</style>