//base6
utils.fileFormatBase64 = function (file) {
  return new Promise((resolve, reject) => {
    var reader = new FileReader();
    reader.readAsDataURL(file);
    reader.onload = function (e) {
      if (e.target.result) {
        resolve(e.target.result)
      } else {
        reject('')
      }

    };
  })

}

//
utils.downloadIamge = function (imgsrc, name) {//
  var image = new Image();
  //  Canvas 
  image.setAttribute("crossOrigin", "anonymous");
  image.onload = function () {
    var canvas = document.createElement("canvas");
    canvas.width = image.width;
    canvas.height = image.height;
    var context = canvas.getContext("2d");
    context.drawImage(image, 0, 0, image.width, image.height);
    var url = canvas.toDataURL("image/png"); //base64

    var a = document.createElement("a"); // a
    var event = new MouseEvent("click"); // 
    a.download = name || "photo"; // 
    a.href = url; // URLa.href
    a.dispatchEvent(event); // a
  };
  image.src = imgsrc;
}
/** */
utils.downloadFile = function (typeName, id, name) {
  let url = `${process.env.VUE_APP_BASEURL}/AnnexAction/download?id=${encodeURI(
    id
  )}&typeName=${encodeURI(typeName)}`
  let a = document.createElement("a");
  document.body.appendChild(a);
  a.href = url;
  a.download = name; //
  a.click(); //  
  window.URL.revokeObjectURL(url); //
  document.body.removeChild(a);

}
/**URL */
utils.changeUrl = async ({ fileName = "", path = "" }) => {
  let res = await downloadFile({ fileName, path })
  const blob = new Blob([res]);
  const url = window.URL.createObjectURL(blob);
  return url
}
