import axios from 'axios'
import qs from 'qs'
import { Message, Loading } from 'element-ui';
import router from '../router'
import store from '../store'


export default function $axios(options) {
  return new Promise((resolve, reject) => {
    var loading = null
    let timeOut = 30000
    let timer = setTimeout(() => {
      loading = options.noLoading ? null : Loading.service({
        // target: '.loadDom',
        target: '.middleContainer',
        lock: true,
        background: 'rgba(255, 255, 255, 0.5)'
      })
    }, 600)
    //
    if (options.headers && options.headers["Content-Type"].indexOf("multipart/form-data") !== -1) {
      timeOut = 60000 * 10
    }

    const instance = axios.create({
      timeout: timeOut,
      withCredentials: true,
      baseURL: process.env.VUE_APP_BASEURL
    })
    //
    instance.interceptors.request.use(
      config => {
        config.headers.common['appCode'] = store.getters.systemCode
        let ticket = sessionStorage.getItem("ticket")
        //ie
        // if (config.url.indexOf("?") != -1) {
        //   config.url = config.url + "&ticket=" + ticket + "&" + Math.floor(Math.random() * 100000)
        // } else {
        //   config.url = config.url + "?ticket=" + ticket + "&" + Math.floor(Math.random() * 100000)
        // }
        if (config.url.indexOf("?") != -1) {
          config.url = config.url + "&" + Math.floor(Math.random() * 100000)
        } else {
          config.url = config.url + "?" + Math.floor(Math.random() * 100000)
        }
        return config
      },
      error => {
        if (error.code === 'ECONNABORTED' && error.message.indexOf('timeout') !== -1) {
          console.log('timeout/ ')
          // return service.request(originalRequest);//
        }
        Message({
          showClose: true,
          message: `:${error}`,
          type: 'error'
        });
        return Promise.reject(error)
      }
    )
    // response 
    instance.interceptors.response.use(
      response => {
        if (!response.data.hasOwnProperty('code')) {
          return response.data;
        }
        switch (response.data.code) {
          case 0:
            return response.data;
          case 10000:
            sessionStorage.clear()
            router.push('/login')
          default:
            Message({
              showClose: true,
              message: `: ${response.data.description}`,
              type: 'error'
            });
            throw response.data.description;
        }
      },
      err => {
        if (err && err.response) {
          switch (err.response.status) {
            case 400:
              err.message = ''
              break
            case 401:
              err.message = ''
              break
            case 403:
              err.message = ''
              break

            case 404:
              err.message = `: ${err.response.config.url}`
              break

            case 408:
              err.message = ''
              break

            case 500:
              err.message = ''
              break

            case 501:
              err.message = ''
              break

            case 502:
              err.message = ''
              break

            case 503:
              err.message = ''
              break

            case 504:
              err.message = ''
              break

            case 505:
              err.message = 'HTTP'
              break

            default:
          }
        }
        Message({
          showClose: true,
          message: `: ${err.message.includes('timeout') ? '' : err}`,
          type: 'error'
        });
        return Promise.reject(err)
      }
    )
    //
    instance(options)
      .then((res) => {
        resolve(res)
        clearTimeout(timer)
        if (loading) {
          loading.close()
        }
        return false
      })
      .catch((error) => {
        reject(error)
        clearTimeout(timer)
        if (loading) {
          loading.close()
        }
      })
  })
}
