<template>
  <div class="memberList h100">
    <el-row class="h100">
      <el-col :span="12" class="h100 memberlist--border">
        <el-input
          v-if="isSeleteName"
          placeholder=""
          size="mini"
          class="tableInput"
          v-model="userName"
        ></el-input>        
        <el-table
          :data="tableConfig.data"
          highlight-current-row
          :header-cell-style="headerStyle"
          ref="gridData"
          @row-click="selectOne"
          height="calc(100% - 41px)"
        >
          <el-table-column v-if="tableConfig.radio" fixed="left" label="-" width="46">
            <template slot-scope="scope">
              <el-radio
                v-model="radioCheck"
                :label="scope.row.id"
                @change="(i)=>radioSelect(scope.row)"
              >
                <i></i>
              </el-radio>
            </template>
          </el-table-column>
          <el-table-column v-else type="selection" fixed="left" width="40"></el-table-column>
          <template v-for="(item,index) in tableConfig.column">
            <el-table-column
              :label="item.label"
              :min-width="item.width"
              :prop="item.prop"
              :key="index"
            ></el-table-column>
          </template>
          
        </el-table>
        <div class="block">
            <el-pagination
              v-if="paginationIsshow"
              @size-change="handleSizeChange"
              @current-change="handleCurrentChange"
              :page-sizes="selectConfig.paginationObj.pageSizes"
              :page-size="selectConfig.queryObj.queryData.pageSize"
              layout="total, prev, pager, next"
              :total="totalNumber">
            </el-pagination>
          </div>
      </el-col>
      <el-col :span="12" class="h100 had-chosed-list">
        <div v-for="(i,index) in hadChoseData" :key="index" class="chosedSelect">
          <label>{{i[tableConfig.column[0].prop]}}</label>
          <i @click="delItem(index,i)" class="el-icon-circle-close select-icon-close"></i>
        </div>
      </el-col>
    </el-row>
  </div>
</template>
<script>
export default {
  props: {
    selectConfig: {
      type: Object,
      default: () => {
        return {};
      },
    },
    chosedData: {
      type: Array,
      default: () => {
        return [];
      },
    },
    isSeleteName: {
      type: Boolean,
      default: true,
    },
  },
  data() {
    return {
      radioCheck: null,
      paginationIsshow:false,
      totalNumber:0,
      tableConfig: {
        radio: true,
        data: [],
        column: [],
      }, //
      hadChoseData: [], //
      userName: "",
    };
  },
  created() {
    this.init();
  },
  watch: {
    userName(val) {
      this.$emit("searchUserName", val);
    },
    "selectConfig.tableDataObj": {
      handler(val, oldVal) {
        if (val.tableArr.length > 0) {
          this.totalNumber = val.total;
        } else {
          this.totalNumber = 0;
        }
        this.tableConfig.data = val.tableArr;
      },
      deep: true
    },
    "tableConfig.data": {
      handler(val) {
        this.checkData();
      },
        
      deep: true,
    },
  },
  methods: {
    init() {
      this.hadChoseData = [...this.chosedData];
      let { radio, data, column } = this.selectConfig;
      this.tableConfig = Object.assign({}, this.tableConfig, {
        radio,
        data,
        column,
      });
      this.paginationIsshow =  this.selectConfig.paginationIsshow;
    },
    headerStyle() {
      return {
        borderRight: "none",
      };
    },
    //
    delItem(index, row) {
      this.hadChoseData.splice(index, 1);
      this.tableConfig.data.map((item) => {
        if (item.id === row.id) {
          this.$refs.gridData.toggleRowSelection(item, false);
        }
      });
      this.radioCheck = null;
    },
    //
    selectAll(selection) {
      let arr = [];
      // if (selection.length) {
      //   [...selection].map((x) => {
      //     if ([...this.hadChoseData].every((y) => y.id !== x.id)) {
      //       this.hadChoseData.push({
      //         ...x,
      //       });
      //     }
      //   });
      // } else {
      //   this.hadChoseData = [...this.hadChoseData].filter((x) =>
      //     [...this.tableConfig.data].every((y) => y.id !== x.id)
      //   );
      // }
    },
    //
    select(selection, row) {
      let rowIndex = -1;
      this.hadChoseData.map((i, index) => {
        if (i.id === row.id) {
          rowIndex = index;
        }
      });
      if (rowIndex != -1) {
        this.hadChoseData.splice(rowIndex, 1);
      } else {
        this.hadChoseData.push({
          ...row,
        });
      }
    },
    //
    checkData() {
      this.$nextTick(() => {
        [...this.tableConfig.data].map((x) => {
          if ([...this.hadChoseData].some((y) => y.id == x.id)) {
            this.$refs.gridData.toggleRowSelection(x, true);
            this.radioCheck = x.id;
          }
        });
        /**, */
        if (this.tableConfig.radio && this.tableConfig.data.length) {
          let obj = this.tableConfig.data[0];
          this.radioCheck = obj.id;
          this.radioSelect(obj);
        }
      });
    },
    //
    radioSelect(row) {
      let arr = [];
      arr.push(Object.assign({}, row));
      this.hadChoseData = [...arr];
    },
    //
    selectOne(row, column) {
      if (this.tableConfig.radio) {
        this.radioCheck = row.id;
        this.radioSelect(row);
      } else {
        this.$refs.gridData.toggleRowSelection(row);
        this.select([], row);
      }
    },
    //
    getSeleteData() {
      let data = JSON.parse(JSON.stringify(this.hadChoseData));
      return data;
    },
    //
    setTableData(data) {
      this.tableConfig.data = [...data];
    },
    //
    handleSizeChange(val) {
      this.selectConfig.queryObj.queryData.page = 1;
      this.selectConfig.queryObj.queryData.pageSize = val;
      this.selectConfig.queryObj.method();
    },
    handleCurrentChange(val) {
      this.selectConfig.queryObj.queryData.page = val;
      this.selectConfig.queryObj.method();
    },
  },
};
</script>
<style  lang="scss" scoped>
.memberList {
  .tableInput {
    width: 80%;
    padding: 4px;
  }
}
.had-chosed-list {
  overflow: auto;
}

.chosedSelect {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 4px 10px;
  line-height: 23px;
}
.memberlist--border {
  border: 1px solid #ebebeb;
}
.select-icon-close {
  cursor: pointer;
}
</style>